<template>
  <v-container
    fluid
    fill-height
  >
    <v-layout
      align-center
      justify-center
    >
      <v-flex
        xs12
        sm8
        lg10
      >
        <v-container
          class="mr-auto"
          fluid
        >

          <v-layout
            column
            align-center
          >
            <v-row justify="center">
              <v-form v-model="isFormValid">
                <v-card
                  ref="form"
                  max-width="1000"
                  v-if="formStep.step1"
                >
                  <v-toolbar
                    dark
                    color="primary"
                  >
                    <v-icon
                      class="mr-2"
                      @click="$router.go(-1)"
                    >mdi-arrow-left-thick</v-icon>
                    <v-toolbar-title>เพิ่มสินค้า</v-toolbar-title>
                  </v-toolbar>
                  <v-card-text>
                    <v-col cols="12">
                      <v-row>
                        <v-col>
                          <v-select
                            prepend-icon="mdi-format-list-bulleted-type"
                            v-model="productForm.idTypeOfArgi"
                            @change="findProductName(productForm.idTypeOfArgi)"
                            item-text="nameTypeOfArgiMk"
                            item-value="idTypeOfArgi"
                            :items="typeOfArgi"
                            :rules="[v => !!v || 'กรุณาเลือกหมวดผลผลิต']"
                            label="หมวดผลผลิต*"
                            required
                          >*</v-select>
                        </v-col>
                        <v-col>
                          <v-select
                            v-model="productForm.idAgri"
                            item-text="nameArgi"
                            item-value="idAgri"
                            :items="productByArgiType"
                            @change="findSpecies(productForm.idAgri)"
                            :rules="[v => !!v || 'กรุณาเลือกชื่อผลผลิต']"
                            label="ชื่อผลผลิต*"
                            required
                          >*</v-select>
                        </v-col>
                        <v-col>
                          <v-select
                            v-model="productForm.speciesArgi"
                            item-text="speciesName"
                            item-value="speciesId"
                            :items="speciesItem"
                            label="สายพันธ์"
                            required
                          >*</v-select>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col>
                          <span class="body-2 font-weight-bold">รายละเอียดสินค้า</span>
                          <v-textarea
                            :rules="[v => !!v || 'กรุณากรอกรายละเอียดสินค้า']"
                            type="text"
                            outlined
                            rows="2s"
                            v-model="productForm.detailOfProduct"
                          ></v-textarea>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col>
                          <span class="body-2 font-weight-bold">ขนาดบรรจุ</span>
                          <v-text-field
                            type="text"
                            outlined
                            dense
                            v-model="productForm.sizeOfProduct"
                          ></v-text-field>
                        </v-col>
                        <v-col>
                          <span class="body-2 font-weight-bold">หน่วย*</span>
                          <v-select
                            v-model="productForm.unit"
                            item-text="nameUnit"
                            item-value="idUnit"
                            :items="unitMk"
                            :rules="[v => !!v || 'กรุณาเลือกหน่วย']"
                            outlined
                            dense
                            required
                          >*</v-select>
                        </v-col>
                        <v-col>
                          <span class="body-2 font-weight-bold">ราคาเริ่มต้น</span>
                          <v-text-field
                            :rules="[v => !!v || 'กรุณากรอกราคาเริ่มต้น']"
                            type="number"
                            number
                            outlined
                            dense
                            v-model="productForm.priceBegin"
                          ></v-text-field>
                        </v-col>
                        <v-col>
                          <span class="body-2 font-weight-bold">ราคาสิ้นสุด</span>
                          <v-text-field
                            :rules="[v => !!v || 'กรุณากรอกราคาสิ้นสุด']"
                            type="number"
                            number
                            outlined
                            dense
                            v-model="productForm.priceEnd"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row>
                        <span class="body-1 font-weight-bold">ช่วงเวลาที่เก็บเกี่ยวผลผลิต</span>
                        <div class="mr-4 ml-4 whiteback userGroupHeight">
                          <v-layout
                            row
                            wrap
                          >
                            <v-flex
                              v-for="(month,index) in monthOfYear"
                              :key="index"
                              xs4
                            >
                              <v-checkbox
                                light
                                :label="month.monthName"
                                :value="month.monthId"
                                v-model="productForm.monthOfProduct"
                              >
                              </v-checkbox>
                            </v-flex>
                          </v-layout>
                        </div>
                      </v-row>
                      <v-row>
                        <v-col>
                          <v-select
                            prepend-icon="mdi-format-list-bulleted-type"
                            v-model="productForm.typeOfStand"
                            item-text="nameTypeOfStand"
                            item-value="idTypeOfStand"
                            :items="typeOfStand"
                            :rules="[v => !!v || 'กรุณาเลือกมาตรฐานผลผลิต']"
                            label="มาตรฐานผลผลิต*"
                            required
                          >*</v-select>
                        </v-col>
                        <v-col>
                          <v-text-field
                            label="การผลิตตามออเดอร์"
                            type="text"
                            v-model="productForm.madeByOrder"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-card-text>
                  <v-card-actions>
                    <v-btn
                      block
                      color="primary"
                      @click="formStep2()"
                      :disabled="!isFormValid"
                    >ถัดไป</v-btn>
                  </v-card-actions>
                </v-card>
                <v-card
                  ref="form"
                  width="600"
                  max-width="1000"
                  v-if="formStep.step2"
                >
                  <v-toolbar
                    dark
                    color="primary"
                  >
                    <v-toolbar-title>เพิ่มสินค้า</v-toolbar-title>
                  </v-toolbar>
                  <v-card-text>
                    <v-col
                      cols="12"
                      v-if="isLoading"
                    >
                      <v-row justify="center">
                        <v-progress-circular
                          :size="350"
                          color="primary"
                          indeterminate
                        ></v-progress-circular>
                      </v-row>
                    </v-col>
                    <v-col
                      cols="12"
                      v-if="!isLoading"
                    >
                      <v-row>
                        <v-col cols="12">
                          <v-file-input
                            type="file"
                            v-model="productForm.imgCurFiles"
                            accept="image/*"
                            small-chips
                            show-size
                            clearable
                            label="เพิ่มรูปภาพ"
                            multiple
                            dense
                            counter
                            ref="file"
                            @change="inputChanged"
                          >
                            <template v-slot:selection="{ text, index }">
                              <v-chip
                                small
                                text-color="white"
                                color="#295671"
                                close
                                @click:close="remove(index)"
                              >
                                {{ text }}
                              </v-chip>
                            </template>
                          </v-file-input>
                          <span class="mb-2 body-2 font-weight-bold">รูปสินค้า (สูงสุด 5 รูป)</span>
                          <v-row>
                          </v-row>
                        </v-col>
                      </v-row>

                      <v-row cols="12">
                        <v-col
                          v-for="(f, i) in oldImg"
                          class="mr-1"
                          :key="i"
                          @click="deleteOldImg(f.productImgId)"
                        >
                          <v-row>
                            <img
                              :key="i"
                              :src="f.imgTitle"
                              width="100"
                              height="100"
                            />
                          </v-row>
                          <v-row>
                            <v-chip>
                              (รูปเดิม) {{ f.imgName }}<v-icon class="ml-2">mdi-backspace-outline</v-icon>
                            </v-chip>
                          </v-row>
                        </v-col>
                      </v-row>

                      <div v-if="productForm.img.length">
                        <h5>รูปสินค้าที่เลือก</h5>
                        <v-chip
                          v-for="(f, i) in productForm.img"
                          class="mr-1"
                          :key="i"
                          @click="remove(i)"
                        >
                          {{ f.name }}<v-icon class="ml-2">mdi-backspace-outline</v-icon>
                        </v-chip>
                        <v-row>
                          <img-preview
                            v-for="file in productForm.img"
                            :key="file.name"
                            :file="file"
                          ></img-preview>
                        </v-row>
                      </div>

                    </v-col>
                  </v-card-text>
                  <v-card-actions>
                    <v-btn
                      block
                      color="primary"
                      @click="submitForm()"
                      :disabled="!isFormValid"
                    >ยืนยันการเพิ่มสินค้า</v-btn>
                  </v-card-actions>
                </v-card>
              </v-form>
            </v-row>
          </v-layout>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";
import imgPreview from "../components/previewImg";

export default {
  components: {
    imgPreview,
  },
  data() {
    return {
      isFormValid: false,
      isLoading: false,
      formStep: {
        step1: true,
        step2: false,
      },
      typeOfArgi: [],
      productForm: {
        idTypeOfArgi: "",
        idAgri: "",
        speciesArgi: "",
        detailOfProduct: "",
        sizeOfProduct: "",
        unit: "",
        priceBegin: "",
        idSellerMember: "",
        priceEnd: "",
        monthOfProduct: [],
        typeOfStand: "",
        madeByOrder: "",
        imgCurFiles: [],
        img: [],
      },
      productByArgiType: [],
      speciesItem: [],
      unitMk: [],
      monthOfYear: [],
      typeOfStand: [],
      userInfo: [],
      argiTd: [],
      image_list: [],
      oldImg: [],
      baseUrl: "https://farmtd.hrdi.or.th/farmdd/admin/api/",
    };
  },
  methods: {
    previewMultiImage: function (event) {
      var input = event.target;
      var count = input.files.length;
      var index = 0;
      if (input.files) {
        while (count--) {
          var reader = new FileReader();
          reader.onload = (e) => {
            this.preview_list.push(e.target.result);
          };
          this.productForm.img.push(input.files[index]);
          this.image_list.push(input.files[index]);
          reader.readAsDataURL(input.files[index]);
          index++;
        }
      }
      console.log(this.productForm.img);
    },
    deleteOldImg(productImgId) {
      if (this.oldImg.length === 1) {
        this.errAlert("ต้องมีรูปภาพอย่างน้อย 1 รูป");
        return;
      }
      this.$swal
        .fire({
          title: "ต้องการลบรูปสินค้าหรือไม่ ?",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "ยืนยัน",
          cancelButtonText: "ยกเลิก",
        })
        .then((result) => {
          if (result.isConfirmed) {
            let formData = new FormData();
            formData.append("productImgId", productImgId);
            axios
              .post(
                "https://farmtd.hrdi.or.th/farmdd/admin/api/product/postDeleteImgProduct.php",
                formData
              )
              .then((res) => {
                this.$swal
                  .fire({
                    icon: "success",
                    title: "ลบรูปภาพสินค้าสำเร็จ",
                    showConfirmButton: false,
                    timer: 1000,
                  })
                  .then((result) => {
                    this.getProductImg();
                  });
              })
              .catch((err) => {
                this.$swal
                  .fire({
                    icon: "error",
                    title: "เกิดข้อผิดพลาด",
                    showConfirmButton: false,
                    timer: 1500,
                  })
                  .then((result) => {
                    window.location.reload();
                  });
              });
          }
        });
    },
    getTypeOFStand() {
      axios
        .get(`${this.baseUrl}product/getTypeOfStand.php`)
        .then((res) => {
          this.typeOfStand = res.data.typeOfStand;
          console.log("gettypeOfStand", this.typeOfStand);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getArgiTd() {
      axios
        .get(`${this.baseUrl}product/getArgiTd.php`)
        .then((res) => {
          this.argiTd = res.data.argiTd;
          console.log("getArgiTd", this.argiTd);
        })
        .then((response) => {
          this.getProductDetail();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    submitForm() {
      this.isFormValid = true;
      
      if (this.productForm.speciesArgi === null) {
        this.productForm.speciesArgi = 0;
      }

      let formData = new FormData();
      formData.append("idTypeOfArgi", this.productForm.idTypeOfArgi);
      formData.append("detailOfProduct", this.productForm.detailOfProduct);
      formData.append("idSellerMember", this.productForm.idSellerMember);
      formData.append(
        "idProductOfMemberMk",
        this.productForm.idProductOfMemberMk
      );
      formData.append("idAgri", this.productForm.idAgri);
      formData.append("madeByOrder", this.productForm.madeByOrder);
      formData.append("monthOfProduct", this.productForm.monthOfProduct);
      formData.append("priceBegin", this.productForm.priceBegin);
      formData.append("priceEnd", this.productForm.priceEnd);
      formData.append("sizeOfProduct", this.productForm.sizeOfProduct);
      formData.append("speciesArgi", this.productForm.speciesArgi);
      formData.append("typeOfStand", this.productForm.typeOfStand);
      formData.append("unit", this.productForm.unit);

      if (this.productForm.img.length > 0) {
        if (
          this.productForm.img.length > 5 ||
          this.productForm.img.length + this.oldImg.length > 5
        ) {
          this.errAlert("ไม่สามาถเพิ่มรูปภาพได้เกิน 5 รูป");
          return;
        }

        formData.append("titleImg", this.productForm.img[0].name);

        var fileSize = 0;
        for (var i = 0; i < this.productForm.img.length; i++) {
          let file = this.productForm.img[i];
          fileSize += file.size;
          formData.append("img[" + i + "]", file);
          console.log(file);
        }

        if (fileSize > 8000000) {
          console.log(this.oldImg);
          this.errAlert("ขนาดไฟล์รวมกันห้ามเกิน 8 MB");
          return;
        }
      } else {
        formData.append("titleImg", this.oldImg[0].imgName);
      }

      for (var pair of formData.entries()) {
        console.log(pair[0], pair[1]);
      }

      this.isLoading = true;
      axios
        .post(`${this.baseUrl}product/postUpdateProduct.php`, formData, {
          maxContentLength: Infinity,
          maxBodyLength: Infinity,
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          console.log(res);
          if (res.data === "True") {
            this.isLoading = false;
            this.$swal
              .fire({
                icon: "success",
                title: "แก้ไขสินค้าสำเร็จ",
                showConfirmButton: false,
                timer: 2000,
              })
              .then(() => {
                this.$router.push({ name: "Home" });
              });
          }
        })
        .catch((err) => {
          if (err) {
            this.$swal
              .fire({
                icon: "error",
                title: "เกิดข้อผิดพลาด",
                text: "ไม่สามารถเพิ่มสินค้าได้",
                showConfirmButton: false,
                timer: 2000,
              })
              .then(() => {
                this.$router.push({ name: "Home" });
              });
          }
        });
    },
    errAlert(msg) {
      this.$swal.fire({
        icon: "info",
        title: msg,
        showConfirmButton: true,
        allowOutsideClick: false,
      });
    },
    getMonthOfYear() {
      axios
        .get(`${this.baseUrl}product/getMonthOfYear.php`)
        .then((res) => {
          this.monthOfYear = res.data.monthOfYear;
          console.log("getMonthOfYear", this.monthOfYear);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getUnitMk() {
      axios
        .get(`${this.baseUrl}product/getUnitMk.php`)
        .then((res) => {
          this.unitMk = res.data.unitMk;
          console.log("unitMk", this.unitMk);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    findSpecies() {
      let agriId = new FormData();
      agriId.append("agriId", this.productForm.idAgri);
      axios
        .post(`${this.baseUrl}product/postSpeciesByArgi.php`, agriId)
        .then((res) => {
          if (res.data.species) {
            this.speciesItem = res.data.species;
            if (this.speciesItem.length === 0) {
              this.speciesItem[0] = {
                agriId: this.productForm.idAgri,
                speciesId: 0,
                speciesName: "ไม่มี",
              };
            }
            console.log("speciesItem", this.speciesItem);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    findProductName(type) {
      this.productByArgiType = [];
      this.productByArgiType = this.argiTd.filter((e) => {
        return e.TypeOfArgiIdTypeOfArgi === type;
      });
      console.log("productByArgiType", this.productByArgiType);
    },
    getTypeOfArgi() {
      axios
        .get(`${this.baseUrl}product/getTypeArgi.php`)
        .then((response) => {
          if (response.data.typeOfArgi !== 0) {
            this.typeOfArgi = response.data.typeOfArgi;
            this.typeOfArgi = this.typeOfArgi.filter((e) => {
              return e.nameTypeOfArgiMk !== null;
            });
            console.log("typeOfArgi", this.typeOfArgi);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    inputChanged(e) {
      console.log("change", e);
      this.productForm.img = [
        ...this.productForm.imgCurFiles,
        ...this.productForm.img,
      ];
    },
    remove(index) {
      this.productForm.img.splice(index, 1);
      this.productForm.imgCurFiles.splice(index, 1);
    },
    formStep2() {
      this.productForm.idSellerMember = localStorage.idMember;
      console.log(this.productForm);
      if (this.formStep.step1) {
        (this.formStep.step1 = false), (this.formStep.step2 = true);
      }
    },
    getProductDetail() {
      let formData = new FormData();
      formData.append(
        "idProductOfMemberMk",
        this.productForm.idProductOfMemberMk
      );

      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/product/getProductDetail.php",
          formData
        )
        .then((res) => {
          if (res.data.productMk.length !== 0) {
            console.log(res.data.productMk);
            this.productForm = res.data.productMk[0];
            this.productForm.typeOfStand = res.data.productMk[0].idTypeOfStand;
            this.productForm.suggestOption = res.data.productMk[0].suggestSta;
            this.productForm.monthOfProduct = res.data.productMk[0].monthOfProduct.split(
              ","
            );
            this.productForm.img = [];

            if (this.productForm.speciesArgi !== "") {
              this.findSpecies();
            }

            if (this.productForm.idTypeOfArgi !== "") {
              this.findProductName(this.productForm.idTypeOfArgi);
            }

            console.log("productDetail", this.productForm);
          }
        })
        .then(() => {
          this.getProductImg();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getProductImg() {
      let formData = new FormData();
      formData.append(
        "idProductOfMemberMk",
        this.productForm.idProductOfMemberMk
      );
      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/product/getProductImg.php",
          formData
        )
        .then((res) => {
          this.oldImg = res.data.productImgMk;
          console.log("oldImg", this.oldImg);
        });
    },
  },
  mounted() {
    this.productForm.idProductOfMemberMk = this.$route.params.idProductOfMemberMk;
    this.getTypeOfArgi();
    this.getArgiTd();
    this.getUnitMk();
    this.getMonthOfYear();
    this.getTypeOFStand();
  },
};
</script>


