import axios from 'axios';
const baseUrl = 'https://farmtd.hrdi.or.th/farmdd/admin/api/';
function checkLogin() {
  if (
    localStorage.idMember === "" ||
    localStorage.idMember === null ||
    localStorage.idMember === undefined
  ) {
    this.$router.push({ name: "Login" });
  }
}
function getPrefix() {
  axios
    .get(baseUrl + 'getPrefix.php')
    .then((res) => {
      return res.data.prefix;
    });
}
export default {
  checkLogin,
  getPrefix
}


