                    </v-col>
                  </v-row>

                  <v-row md="4">
                    <v-col>
                      <v-select
                        prepend-icon="mdi-map-marker-circle"
                        v-model="buyerForm.provinceCode"
                        item-text="name"
                        item-value="code"
                        :items="allProvince"
                        :rules="[v => !!v || 'กรุณาเลือกจังหวัด']"
                        @change="findAmphurBuyer(buyerForm.provinceCode)"
                        label="จังหวัด*"
