</template>

<script>
import axios from "axios";
import app from "../functions/app";

export default {
  data: () => ({
    imagePreviewURL: null,
    isFormValid: false,
    prefix: [],
    confirmPassword: "",
    isUsernameExist: false,
    buyerForm: {
      prefix: "",
      firstName: "",
      lastName: "",
      username: "",
      phoneNumber: "",
      email: "",
      idCard: "",
      password: "",
      img: [],
      provinceCode: "",
      typeOfMember: "",
      amphurCode: "",
      tambolCode: "",
      lineId: "",
      postcode: "",
      facebookName: "",
      address: "",
      moo: "",
      road: "",
      questionOfVisit: "",
      questionOfObjective: "",
    },
    targetName: [],
    targetFilter: [],
    allProvince: [],
    buyerAmphur: [],
    buyerTambol: [],
    showEtc: {
      visit: false,
      objective: false,
    },
    isUsernameExist: false,
    baseUrl: "https://farmtd.hrdi.or.th/farmdd/admin/api/",
    firstNameRules: [(v) => !!v || "กรุณากรอกชื่อ"],
    lastNameRules: [(v) => !!v || "กรุณากรอกนามสกุล"],
    emailRules: [(v) => /.+@.+/.test(v) || "อีเมลต้องอยู่ในรูปแบบที่ถูกต้อง"],
    phoneRules: [
      (v) => !v.length < 10 || "หมายเลขโทรศัพท์ต้องมีจำนวน 10 หลัก",
      (v) => !!v || "กรุณากรอกหมายเลขโทรศัพท์",
      (v) =>
        /^0[0-9]{9}/.test(v) || "หมายเลขโทรศัพท์ต้องอยู่ในรูปแบบที่ถูกต้อง",
    ],
    passwordRules: [
      (v) => !!v || "กรุณากรอกรหัสผ่าน",
      (v) => /[A-Z]+/.test(v) || "ต้องมีตัวอักษรตัวใหญ่ในรหัสผ่าน",
      (v) => /[0-9]/g.test(v) || "ตัองมีตัวเลขในรหัสผ่าน",
      (v) => v.length >= 6 || "รหัสผ่านต้องมีอย่างน้อย 6 ตัว",
      (v) => /^[a-zA-Z0-9äöüÄÖÜ]*$/.test(v) || "ไม่อนุญาตให้ใช้ตัวอักษรพิเศษ",
      (v) =>
        (v || "").indexOf(" ") < 0 || "ไม่อนุญาตให้ใช้รหัสผ่านที่มีช่องว่าง",
    ],
  }),
  methods: {
    checkUsername(username) {
      this.isUsernameExist = false;
      var usernameFormData = new FormData();
      usernameFormData.append("username", username);

      var userForm = {};
      usernameFormData.forEach(function (value, key) {
        userForm[key] = value;
      });
      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/checkUsername.php",
          JSON.stringify(userForm)
        )
        .then((res) => {
          if (res.data.username[0].username.length > 0) {
            this.isUsernameExist = true;
          }
        });
    },
    previewImg(payload) {
      const file = payload;
      this.buyerForm.img = file;
      if (file) {
        this.imagePreviewURL = URL.createObjectURL(file);
        URL.revokeObjectURL(file);
      } else {
        this.imagePreviewURL = null;
      }
    },
    submitForm() {
      console.log(this.buyerForm);
      if (this.buyerForm.prefix === "") {
        this.errAlert("กรุณาเลือกคำนำหน้า");
        return;
      }

      if (this.buyerForm.firstName === "") {
        this.errAlert("กรุณากรอกชื่อ");
        return;
      }

      if (this.buyerForm.lastName === "") {
        this.errAlert("กรุณากรอกนามสกุล");
        return;
      }

      if (this.buyerForm.gender === "") {
        this.errAlert("กรุณากรอกเพศ");
        return;
      }

      if (this.buyerForm.username === "") {
        this.errAlert("กรุณากรอก Username");
        return;
      }

      if (this.buyerForm.username.indexOf(" ") >= 0) {
        console.log("ไม่อนุญาติให้มีช่องว่างในรหัสผ่าน");
      }

      if (this.buyerForm.password === "") {
        this.errAlert("กรุณากรอก Password");
        return;
      }

      if (this.buyerForm.confirmPassword === "") {
        this.errAlert("กรุณายืนยัน Password");
        return;
      }

      if (this.buyerForm.email === "") {
        this.errAlert("กรุณากรอกอีเมล");
        return;
      }

      if (this.buyerForm.phoneNumber === "") {
        this.errAlert("กรุณากรอกหมายเลขโทรศัพท์");
        return;
      }

      if (this.buyerForm.provinceCode === "") {
        this.errAlert("กรุณาเลือกจังหวัด");
        return;
      }

      if (this.buyerForm.amphurCode === "") {
        this.errAlert("กรุณาเลือกอำเภอ");
        return;
      }

      if (this.buyerForm.tambolCode === "") {
        this.errAlert("กรุณาเลือกตำบล");
        return;
      }

      if (
        this.buyerForm.questionOfVisit === "" ||
        this.buyerForm.questionOfObjective === ""
      ) {
        this.errAlert("กรุณาเลือกเหตุผล");
        return;
      }

      var formData = new FormData();
      formData.append("address", this.buyerForm.address);
      formData.append("amphurCode", this.buyerForm.amphurCode);
      formData.append("email", this.buyerForm.email);
      formData.append("facebookName", this.buyerForm.facebookName);
      formData.append("firstName", this.buyerForm.firstName);
      formData.append("idCard", this.buyerForm.idCard);
      formData.append("firstName", this.buyerForm.firstName);
      formData.append("gender", this.buyerForm.gender);
      formData.append("img", this.buyerForm.img);
      formData.append("typeOfMember", this.buyerForm.typeOfMember);
      formData.append("lastName", this.buyerForm.lastName);
      formData.append("lineId", this.buyerForm.lineId);
      formData.append("moo", this.buyerForm.moo);
      formData.append("postcode", this.buyerForm.postcode);
      formData.append("password", this.buyerForm.password);
      formData.append("phoneNumber", this.buyerForm.phoneNumber);
      formData.append("statusOfMember", this.buyerForm.statusOfMember);
      formData.append("prefix", this.buyerForm.prefix);
      formData.append("provinceCode", this.buyerForm.provinceCode);
      formData.append(
        "questionOfObjective",
        this.buyerForm.questionOfObjective
      );
      formData.append("questionOfVisit", this.buyerForm.questionOfVisit);
      formData.append("road", this.buyerForm.road);
      formData.append("tambolCode", this.buyerForm.tambolCode);
      formData.append("username", this.buyerForm.username);

      var contentForm = {};
      formData.forEach(function (value, key) {
        console.log(value);
        contentForm[key] = value;
      });

      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/member/postUpdateBuyer.php",
          formData,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        )
        .then((res) => {
          if (res.data === "True") {
            console.log(res);
            this.$swal
              .fire({
                icon: "success",
                title: "แก้ไขสมาชิกทั่วไป / ผู้ซื้อสำเร็จ",
                showConfirmButton: false,
                timer: 2000,
              })
              .then(() => {
                this.$router.push({ name: "UserProfile" });
              });
          } else {
            this.$swal
              .fire({
                icon: "error",
                title: "เกิดข้อผิดพลาด",
                text: "ไม่สามารถแก้ไขสมาชิกได้",
                showConfirmButton: false,
                timer: 2000,
              })
              .then(() => {
                this.$router.push({ name: "UserProfile" });
              });
          }
        });
    },
    getPrefix() {
      axios.get(`${this.baseUrl}getPrefix.php`).then((res) => {
        this.prefix = res.data.prefix;
        console.log("prefix", this.prefix);
      });
    },
    checkLogin() {
      if (
        localStorage.idMember === "" ||
        localStorage.idMember === null ||
        localStorage.idMember === undefined
      ) {
        this.$router.push({ name: "Login" });
      }
    },
    questionBuyer(select) {
      if (select === "อื่น ๆ") {
        this.showEtc.visit = true;
      } else {
        this.showEtc.visit = false;
      }
    },
    questionObjBuyer(select) {
      if (select === "อื่น ๆ") {
        this.showEtc.objective = true;
      } else {
        this.showEtc.objective = false;
      }
    },
    onFileChange(e) {
      const file = e.target.files[0];
      this.buyerForm.img = file;
      this.url = URL.createObjectURL(file);
    },
    errAlert(msg) {
      this.$swal.fire({
        icon: "info",
        title: msg,
        showConfirmButton: true,
        allowOutsideClick: false,
      });
    },
    findAmphurBuyer(provinceId) {
      axios
        .get(
          `https://farmtd.hrdi.or.th/farmdd/admin/api/getBuyerAmphur.php?provinceId=${provinceId}`
        )
        .then((res) => {
          if (res.data.buyerAmphur.length > 0) {
            this.buyerAmphur = res.data.buyerAmphur;
            this.buyerAmphurOption = this.buyerAmphur.map((e) => ({
              value: e.ampCode,
              text: e.ampT,
            }));
          }
          console.log("getBuyerAmphur", this.buyerAmphur);
        });
    },
    getAllProvince() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/admin/api/getAllprovince.php")
        .then((res) => {
          if (res.data.allProvince.length > 0) {
            this.allProvince = res.data.allProvince;
            this.provinceOption = this.allProvince.map((e) => ({
              value: e.code,
              text: e.name,
            }));
          }
          console.log("getAllprovince", this.allProvince);
        });
    },
    findTambolBuyer(provinceId, amphurId) {
      axios
        .get(
          `https://farmtd.hrdi.or.th/farmdd/admin/api/getBuyerTambol.php?provinceId=${provinceId}&amphurId=${amphurId}`
        )
        .then((res) => {
          if (res.data.buyerTambol.length > 0) {
            this.buyerTambol = res.data.buyerTambol;
            this.buyerTambolOption = this.buyerTambol.map((e) => ({
              value: e.tamCode,
              text: e.tamT,
            }));
          }
          console.log("getBuyerTambol", this.buyerTambol);
        });
    },
    getUserInfo() {
      let usernameData = new FormData();
      usernameData.append("username", localStorage.user);
      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/member/postBuyerFromId.php",
          usernameData
        )
        .then((res) => {
          if (res.data.buyerUser[0] !== 0) {
            this.imagePreviewURL = `https://farmtd.hrdi.or.th/farmdd/admin/upload/profile/${res.data.buyerUser[0].buyerImg}`;
            this.confirmPassword = res.data.buyerUser[0].pass;
            this.buyerForm = {
              prefix: res.data.buyerUser[0].idPrefix,
              firstName: res.data.buyerUser[0].firstName,
              lastName: res.data.buyerUser[0].lastName,
              username: res.data.buyerUser[0].username,
              phoneNumber: res.data.buyerUser[0].phoneNumber,
              email: res.data.buyerUser[0].email,
              idCard: res.data.buyerUser[0].idCard,
              password: res.data.buyerUser[0].pass,
              gender: res.data.buyerUser[0].gender,
              provinceCode: res.data.buyerUser[0].provinceCode,
              typeOfMember: res.data.buyerUser[0].typeOfMember,
              statusOfMember: res.data.buyerUser[0].statusOfMember,
              amphurCode: res.data.buyerUser[0].ampCode,
              postcode: res.data.buyerUser[0].postCode,
              tambolCode: res.data.buyerUser[0].tamCode,
              lineId: res.data.buyerUser[0].lineID,
              facebookName: res.data.buyerUser[0].facebookName,
              address: res.data.buyerUser[0].address,
              moo: res.data.buyerUser[0].moo,
              road: res.data.buyerUser[0].road,
              questionOfVisit: res.data.buyerUser[0].questionOfVisit,
              questionOfObjective: res.data.buyerUser[0].questionOfObjective,
            };
            this.findAmphurBuyer(this.buyerForm.provinceCode);
            this.findTambolBuyer(
              this.buyerForm.provinceCode,
              this.buyerForm.amphurCode
            );
            if (
              this.buyerForm.questionOfVisit !== "ค้นหาจาก Google" &&
              this.buyerForm.questionOfVisit !==
                "เข้ามาในเวบไซด์ของสถาบันวิจัยและพัฒนาพื้นที่สูง" &&
              this.buyerForm.questionOfVisit !== "เพื่อน / คนรู้จักแนะนำ"
            ) {
              this.showEtc.visit = true;
            }
            if (
              this.buyerForm.questionOfObjective !== "เข้ามาเยี่ยมชม" &&
              this.buyerForm.questionOfObjective !== "เข้ามาซื้อผลผลิต" &&
              this.buyerForm.questionOfObjective !==
                "เข้ามาอ่าน ติดตามข้อมูลข่าวสาร"
            ) {
              this.showEtc.objective = true;
            }
            console.log("postBuyerFromId", res.data.buyerUser[0]);
            console.log("buyerForm", this.buyerForm);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.checkLogin();
    this.getPrefix();
    this.getAllProvince();
    this.getUserInfo();
  },
  created() {},
};
</script>


