                    value="3"
                    hide-details
                  ></v-checkbox>
                  <v-checkbox
                    @change="filterMonth(month)"
                    v-model="month"
                    label="มิ.ย."
                    value="6"
                    hide-details
                  ></v-checkbox>
                </v-col>
                <v-col
                  cols="12"
