              </v-row>

            </v-container>

          </v-card>
        </div>
        <div class="col-md-9 col-sm-9 col-xs-12">
          <v-row dense>
            <v-col
              cols="12"
              sm="8"
              class="pl-6 pt-6"
            >
            </v-col>
            <v-col
              cols="12"
              sm="4"
            >
              <v-text-field
                @keypress.enter="show"
                v-model="productName"
                label="ค้นหาสินค้าจากชื่อ"
                dense
              >
                <template v-slot:append>
                  <v-btn
                    depressed
                    tile
                    color="primary"
                    class="ma-0"
                    @click="filterName(productName)"
                  >
                    <v-icon>mdi-magnify</v-icon>
                  </v-btn>
                </template>
              </v-text-field>
              <v-select
                class="pa-0"
                v-model="select"
                :items="options"
                @change="sortByPrice(select)"
                style="margin-bottom: -20px;"
                outlined
                dense
              ></v-select>
            </v-col>
          </v-row>

          <v-breadcrumbs
            class="pb-1 body-1 font-weight-bold"
            :items="breadcrums"
          ></v-breadcrumbs>

          <v-divider></v-divider>

          <div class="row text-center">
            <v-row
              class="pa-5"
              justify="center"
              v-if="checkProductSize && !isLoading"
            >
              <h1>ไม่พบข้อมูล...</h1>
            </v-row>
            <v-row
              class="pa-5"
              justify="center"
              v-if="isLoading"
            >
              <v-progress-circular
                :size="200"
                color="primary"
                indeterminate
              ></v-progress-circular>
            </v-row>
            <div
              class="col-md-4 col-sm-6 col-xs-12"
              :key="index"
              v-for="(product, index) in visibleContentPages"
            >
              <v-hover v-slot:default="{ hover }">
                <v-card
                  class="mx-auto"
                  color="grey lighten-4"
                  max-width="600"
                  height="100%"
                >
                  <v-img
                    class="white--text align-end"
                    :aspect-ratio="16/9"
                    height="200px"
                    :src="product.titleImg"
                    :lazy-src="product.titleImg"
                  >
                    <template v-slot:placeholder>
                      <v-row
                        class="fill-height ma-0"
                        align="center"
                        justify="center"
                      >
                        <v-progress-circular
                          indeterminate
                          color="grey lighten-5"
                        ></v-progress-circular>
                      </v-row>
                    </template>
                    <v-expand-transition>
                      <div
                        v-if="hover"
                        class="d-flex transition-fast-in-fast-out white darken-4 v-card--reveal display-3"
                        style="height: 100%;"
                      >
                        <v-btn
                          v-if="hover"
                          @click="productDetail(product.idProductOfMemberMk)"
                          color="primary"
                          outlined
                        >รายละเอียด</v-btn>
                      </div>
                    </v-expand-transition>
                  </v-img>
                  <v-card-text class="pa-0">
                    <v-row
                      cols="12"
                      justify="center"
                    >
                      <v-card-title class="indigo--text text--darken-4 font-weight-black">
                        {{ product.nameArgi }}
                      </v-card-title>
                    </v-row>
                    <v-card-subtitle class="font-weight-bold pt-0">
                      <h4>{{ product.groupName }}</h4>
                    </v-card-subtitle>

                    <v-row justify="center">
                      <v-chip class="body-2 green white--text mb-1">
                        <h5>{{ product.nameTypeOfArgi }}</h5>
                      </v-chip>
                    </v-row>
                    <v-row justify="center"><span class="body-1 font-weight-black pa-1">{{ product.priceBegin === product.priceEnd ? `฿${product.priceEnd}` : 
                      `฿${product.priceBegin} - ฿${product.priceEnd}` }} / {{ product.nameUnit }}</span></v-row>
                  </v-card-text>
                </v-card>
              </v-hover>
            </div>
          </div>
          <div class="text-center mt-12">
            <v-pagination
              v-model="page"
              :length="Math.ceil(productMk.length/perPage)"
            ></v-pagination>
          </div>
        </div>
      </div>
    </v-container>
  </div>
</template>
<style>
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: 0.8;
  position: absolute;
  width: 100%;
}
</style>
<script>
import axios from "axios";

export default {
  data: () => ({
    isLoading: false,
    page: 1,
    perPage: 12,
    productName: "",
    baseUrl: "https://farmtd.hrdi.or.th/farmdd/admin/api/",
    productMk: [],
    cloneProductMk: [],
    countType: {
      first: 0,
      second: 0,
      third: 0,
      fourth: 0,
      fifth: 0,
    },
    typeItemsSelected: 0,
    range: [0, 1000],
    select: "ตัวกรองการค้นหา",
    options: ["ตัวกรองการค้นหา", "ราคา: ต่ำ ถึง สูง", "ราคา: สูง ถึง ต่ำ"],
    typeItems: [],
    page: 1,
    breadcrums: [
      {
        text: "แสดงสินค้าจากประเภทเกษตรกร",
        disabled: false,
        href: "#",
      },
      {
        text: "ทั้งหมด",
        disabled: false,
        href: "#",
      },
    ],
    month: [],
    min: null,
    max: null,
  }),
  computed: {
    visibleContentPages() {
      return this.productMk.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      );
    },
    checkProductSize: function () {
      if (this.productMk.length === 0) {
        return true;
      }
    },
    filterProductsByRange: function () {
      console.log(working);
      return this.productMk.filter((product) =>
        product.priceBegin > 0 && product.priceEnd < this.range ? product : ""
      );
    },
  },
  methods: {
    productDetail(idProduct) {
      this.$router.push({
        name: "ProductDetail",
        params: { idProductOfMemberMk: idProduct },
      });
    },
    filterMonth(month) {
      console.log(this.month);
      this.productMk = [...this.cloneProductMk];
      if (this.typeItemsSelected === 0 || this.typeItemsSelected === "0") {
        month.map((e, i) => {
          this.productMk = this.productMk.filter((product) => {
            const productArr = product.monthOfProduct.split(",").sort();
            return productArr.includes(e);
          });
        });
        this.isLoading = false;
      } else {
        month.map((e, i) => {
          this.productMk = this.productMk.filter((product) => {
            const productArr = product.monthOfProduct.split(",").sort();
            return (
              productArr.includes(e) &&
              parseInt(product.idTypeOfSellerMk) === this.typeItemsSelected
            );
          });
        });
      }

      console.log(this.productMk);
    },
    filterName(name) {
      this.productMk = [...this.cloneProductMk];
      this.productMk = this.productMk.filter((e) => {
        return name === e.nameArgi;
      });

      this.productMk = this.productMk.filter((e) => {
        return name === e.nameArgi;
      });
    },
    filterRange() {
      this.productMk = [...this.cloneProductMk];
      console.log(this.typeItemsSelected);
      if (this.typeItemsSelected === 0 || this.typeItemsSelected === "0") {
        this.productMk = this.productMk.filter((e) => {
          if (e.idTypeOfSellerMk)
            return (
              parseInt(e.priceBegin) >= this.min &&
              parseInt(e.priceEnd) <= this.max
            );
        });
      } else {
        this.productMk = this.productMk.filter((e) => {
          if (e.idTypeOfSellerMk)
            return (
              parseInt(e.priceBegin) >= this.min &&
              parseInt(e.priceEnd) <= this.max &&
              parseInt(e.idTypeOfSellerMk) === this.typeItemsSelected
            );
        });
      }
    },
    farmerFilter(type) {
      this.page = 1;
      this.month = [];
      const typeNum = parseInt(type);
      switch (typeNum) {
        case 0:
          this.breadcrums[1].text = "ทั้งหมด";
          break;
        case 1:
          this.breadcrums[1].text = "เกษตรกรรายเดี่ยว";
          break;
        case 2:
          this.breadcrums[1].text = "วิสาหกิจชุมชน";
          break;
        case 3:
          this.breadcrums[1].text = "สหกรณ์";
          break;
        case 4:
          this.breadcrums[1].text = "กลุ่มเกษตรกร";
          break;
        case 4:
          this.breadcrums[1].text = "กลุ่มไม่เป็นทางการ";
          break;
      }
      this.select = "ตัวกรองการค้นหา";
      this.min = 0;
      this.mix = 0;
      if (type === "0") {
        return (this.productMk = [...this.cloneProductMk]);
      }

      this.productMk = [...this.cloneProductMk];
      this.productMk = this.productMk.filter((e) => {
        return parseInt(e.idTypeOfSellerMk) === type;
      });
    },
    sortByPrice(sort) {
      if (sort === "ราคา: ต่ำ ถึง สูง") {
        this.productMk.sort(
          (a, b) => parseFloat(a.priceEnd) - parseFloat(b.priceEnd)
        );
      }

      if (sort === "ราคา: สูง ถึง ต่ำ") {
        this.productMk.sort(
          (a, b) => parseFloat(b.priceEnd) - parseFloat(a.priceEnd)
        );
      }
    },
    getAllProduct() {
      this.isLoading = true;
      axios
        .get(`${this.baseUrl}/product/getAllProduct.php`)
        .then((res) => {
          if (res.data.productMk !== 0) {
            res.data.productMk.reverse();
            this.productMk = res.data.productMk;
            this.cloneProductMk = res.data.productMk;
            console.log("productMk", this.productMk);
            this.productMk.map((e, i) => {
              switch (e.idTypeOfSellerMk) {
                case "1":
                  console.log("True");
                  this.countType.first++;
                  break;
                case "2":
                  this.countType.second++;
                  break;
                case "3":
                  this.countType.third++;
                  break;
                case "4":
                  this.countType.fourth++;
                  break;
                case "5":
                  this.countType.fifth++;
                  break;
              }
            });
            this.isLoading = false;
            console.log("countType", this.countType);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getTypeOfSeller() {
      axios
        .get(`${this.baseUrl}/member/getTypeOfSeller.php`)
        .then((res) => {
          if (res.data.sellerType !== 0) {
            this.typeItems = res.data.sellerType;
            this.typeItems.unshift({
              idTypeOfSellerMk: "0",
              nameTypeOfSeller: "แสดงทุกประเภท",
            });
            console.log("typeItems", this.typeItems);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.getAllProduct();
    this.getTypeOfSeller();
  },
};
</script>



