import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Shop from '@/components/Shop'
import Product from '@/components/Product'
import Blog from '@/components/Blog'
import Market from '@/components/Market'
import Post from '@/components/Post'
import Cart from '@/components/Cart'
import Content from '@/components/Content'
import Layout from '@/components/Layout'
import Login from '@/components/Login'
import Register from '@/components/Register'
import AboutUs from '@/components/AboutUs'
import TypeList from '@/components/TypeList'
import FindProduct from '@/components/FindProduct'
import UserProfile from '@/components/UserProfile'
import EditProfile from '@/components/EditProfile'
import EditSeller from '@/components/EditSeller'
import ProductDetail from '@/components/ProductDetail'
import AddProduct from '@/components/Addproduct'
import EditProduct from '@/components/EditProduct'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  linkActiveClass: "active",
  base: '/farmdd',
  routes: [
    {
      path: '/',
      component: Layout,
      children: [
        {
          path: '/',
          component: Home,
          name: 'Home'
        },
        {
          path: '/shop',
          component: Shop,
          name: 'Shop'
        },
        {
          path: '/addproduct',
          component: AddProduct,
          name: 'AddProduct'
        },
        {
          path: '/editproduct/:idProductOfMemberMk',
          component: EditProduct,
          name: 'EditProduct'
        },
        {
          path: '/editprofile',
          component: EditProfile,
          name: 'EditProfile'
        },
        {
          path: '/editprofile',
          component: EditProfile,
          name: 'EditProfile'
        },
        {
          path: '/product',
          component: Product,
          name: 'Product'
        },
        {
          path: '/market',
          component: Market,
          name: 'Market'
        },
        {
          path: '/blog',
          component: Blog,
          name: 'Blog'
        },
        {
          path: '/aboutus',
          component: AboutUs,
          name: 'AboutUs'
        },
        {
          path: '/userprofile',
          component: UserProfile,
          name: 'UserProfile'
        },
        {
          path: '/editseller',
          component: EditSeller,
          name: 'EditSeller'
        },
        {
          path: '/findproduct',
          component: FindProduct,
          name: 'FindProduct'
        },
        {
          path: '/typelist/:idTypeOfArgi/:nameTypeOfArgi',
          component: TypeList,
          name: 'TypeList'
        },
        {
          path: '/content/:page',
          component: Content,
          name: 'Content'
        },
        {
          path: '/productdetail/:idProductOfMemberMk',
          component: ProductDetail,
          name: 'ProductDetail'
        },
        {
          path: '/post/:id',
          component: Post,
          name: 'Post'
        },
        {
          path: '/register',
          component: Register,
          name: 'Register'
        },
        {
          path: '/cart',
          component: Cart,
          name: 'Cart'
        },
        {
          path: '/login',
          component: Login,
          name: 'Login'
        }
      ]
    }
  ],
},

)



