<template>
  <v-app id="inspire">
    <v-sheet class="overflow-hidden">
      <v-navigation-drawer
        app
        v-model="drawer"
        absolute
        temporary
      >
        <v-list-item v-if="loginItem.user">
          <!-- <v-list-item-avatar>
            <v-img src="https://randomuser.me/api/portraits/men/78.jpg"></v-img>
          </v-list-item-avatar> -->

          <v-list-item-content>
            <router-link
              to="/userprofile"
              style="text-decoration: none;"
              class="mr-3 mb-1"
            >
              <v-list-item-title>{{ `${loginItem.firstName} ${loginItem.lastName}` }}</v-list-item-title>
            </router-link>
          </v-list-item-content>
        </v-list-item>

        <v-divider></v-divider>

        <v-list dense>
          <v-list-item
            v-for="item in items"
            :key="item.title"
            link
          >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title @click="goToPage(item.title)">{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-navigation-drawer>
    </v-sheet>
    <v-app-bar
      :clipped-left="$vuetify.breakpoint.lgAndUp"
      fixed
      app
      color="info"
      dark
    >
      <v-app-bar-nav-icon
        v-if="$vuetify.breakpoint.xsOnly"
        @click.stop="showDrawer"
      ></v-app-bar-nav-icon>
      <v-toolbar-title style="width: 350px">

        <router-link
          to="/"
          class="white--text"
          style="text-decoration: none"
        >
          ของดีบนดอย Online
        </router-link>
      </v-toolbar-title>
      <!-- <v-text-field
        flat
        solo-inverted
        hide-details
        prepend-inner-icon="mdi-magnify"
        label="Search"
        class="hidden-sm-and-down pl-10 ml-4"
      /> -->
      <v-spacer />

      <div v-if="!loginItem.user && $vuetify.breakpoint.smAndUp">
        <v-icon class="mr-1">mdi-account-circle</v-icon>
        <router-link
          to="/login"
          style="text-decoration: none;"
        >
          <span class="white--text">เข้าสู่ระบบ</span>
        </router-link>
      </div>
      <v-row
        col="12"
        v-if="!loginItem.user && $vuetify.breakpoint.xsOnly"
      >
        <router-link
          to="/login"
          style="text-decoration: none;"
        >
          <v-icon class="ml-2 mt-1">mdi-account-circle</v-icon>
          <span class="white--text">Login</span>
        </router-link>
      </v-row>

      <v-row
        v-if="loginItem.user && $vuetify.breakpoint.smAndUp"
        align="end"
      >
        <v-spacer></v-spacer>
        <v-icon class="mr-1 mb-1">mdi-account-circle</v-icon>
        <router-link
          to="/userprofile"
          style="text-decoration: none;"
          class="mr-3 mb-1"
        >
          <span class="white--text"> {{ `${loginItem.firstName} ${loginItem.lastName}` }}</span>
        </router-link>

        <v-btn
          class="mx-2"
          fab
          dark
          small
          color="error"
          @click="logout"
        >
          <v-icon>mdi-logout</v-icon>
        </v-btn>
      </v-row>
      <v-row
        v-if="loginItem.user && $vuetify.breakpoint.xsOnly"
        align="end"
      >
        <v-spacer></v-spacer>

        <v-btn
          class="mx-2"
          fab
          dark
          small
          color="error"
          @click="logout"
        >
          <v-icon>mdi-logout</v-icon>
        </v-btn>
      </v-row>
      <!-- <v-btn
        v-on="on"
        icon
      >
        <v-badge
          content="2"
          value="2"
          color="green"
          overlap
        >
          <v-icon>mdi-bell</v-icon>
        </v-badge>
      </v-btn> -->
      <!-- <v-btn
        v-on="on"
        href="/cart"
        icon
      >
        <v-badge
          content="2"
          value="2"
          color="green"
          overlap
        >
          <v-icon>mdi-cart</v-icon>
        </v-badge>
      </v-btn> -->
    </v-app-bar>
    <v-content>
      <v-bottom-navigation
        v-if="$vuetify.breakpoint.smAndUp"
        :value="activeBtn"
        color="primary"
        horizontal
        active-class="active"
        style="overflow-x: scroll;"
      >
        <v-btn
          to="/"
          class="body-2 font-weight-bold"
        >หน้าแรก</v-btn>
        <v-btn
          to="/shop"
          class="body-2 font-weight-bold"
        >
          <span>ค้นหาเกษตรกร & สินค้า</span>
        </v-btn>
        <v-btn
          to="/findproduct"
          class="body-2 font-weight-bold"
        >
          <span>ค้นหาแหล่งสินค้า</span>
        </v-btn>
        <v-btn
          to="/market"
          class="body-2 font-weight-bold"
        >
          <span>สถานการณ์ราคาผลผลิต</span>
        </v-btn>
        <v-btn
          to="/content/1"
          class="body-2 font-weight-bold"
        >
          <span>แนะนำผลผลิต</span>
        </v-btn>
        <v-btn
          to="/aboutus"
          class="body-2 font-weight-bold"
        >
          <span>เกี่ยวกับเรา</span>
        </v-btn>
      </v-bottom-navigation>
    </v-content>
    <router-view />
    <v-footer :padless="true">
      <v-card
        flat
        tile
        width="100%"
        class="info white--text text-center"
      >
        <v-card-text>
          <v-btn
            to="/"
            class="mx-4 white--text"
            icon
          >
            <v-icon size="24px">mdi-home</v-icon>
          </v-btn>
          <v-btn
            to="/shop"
            class="mx-4 white--text"
            icon
          >
            <v-icon size="24px">mdi-account-supervisor</v-icon>
          </v-btn>
          <v-btn
            to="/findproduct"
            class="mx-4 white--text"
            icon
          >
            <v-icon size="24px">mdi-shopping</v-icon>
          </v-btn>
          <v-btn
            to="/market"
            class="mx-4 white--text"
            icon
          >
            <v-icon size="24px">mdi-store</v-icon>
          </v-btn>
          <v-btn
            to="/content/1"
            class="mx-4 white--text"
            icon
          >
            <v-icon size="24px">mdi-fruit-cherries</v-icon>
          </v-btn>
          <v-btn
            to="/aboutus"
            class="mx-4 white--text"
            icon
          >
            <v-icon size="24px">mdi-information</v-icon>
          </v-btn>

        </v-card-text>

        <v-card-text class="white--text pt-0">
        </v-card-text>

        <v-divider></v-divider>

        <v-card-text class="white--text">
          {{ new Date().getFullYear() }} — <strong>ของดีบนดอย Online</strong>

          <v-row
            justify="center"
            class="mt-2"
          >
            <v-icon
              size="24px"
              class="white--text mr-1 mb-1 ml-2"
            >mdi-home</v-icon>ที่อยู่: 65 หมู่ 1 ถ.สุเทพ ต.สุเทพ อ.เมือง จ.เชียงใหม่ 50200
            <v-icon
              size="24px"
              class="white--text mr-1 mb-1 ml-2"
            >mdi-phone</v-icon>โทรศัพท์: 053-328496-8
          </v-row>

          <v-row justify="center">
            <v-icon
              size="24px"
              class="white--text mr-1 mb-1 ml-2"
            >mdi-fax</v-icon>โทรสาร: 053-328494, 053-328229
            <v-icon
              size="24px"
              class="white--text mr-1 mb-1 ml-2"
            >mdi-email</v-icon>info@hrdi.or.th
          </v-row>
        </v-card-text>
      </v-card>
    </v-footer>
  </v-app>
</template>
<script>
export default {
  data() {
    return {
      drawer: false,
      group: null,
      isMobile: false,
      loginItem: {},
      items: [
        { title: "หน้าแรก", icon: "mdi-home" },
        { title: "ค้นหาเกษตรกร & สินค้า", icon: "mdi-account-supervisor" },
        { title: "ค้นหาแหล่งสินค้า", icon: "mdi-shopping" },
        { title: "สถานการณ์ราคาผลผลิต", icon: "mdi-store" },
        { title: "แนะนำผลผลิต", icon: "mdi-fruit-cherries" },
        { title: "เกี่ยวกับเรา", icon: "mdi-information" },
      ],
      activeBtn: 1,
    };
  },
  computed: {},
  watch: {
    group() {
      this.drawer = false;
    },
  },
  methods: {
    showDrawer() {
      this.drawer = !this.drawer;
      window.scrollTo(0,0);
    },
    goToPage(title) {
      switch (title) {
        case "หน้าแรก":
          this.$router.push({ name: "Home" });
          break;
        case "ค้นหาเกษตรกร & สินค้า":
          this.$router.push({ name: "Shop" });
          break;
        case "ค้นหาแหล่งสินค้า":
          this.$router.push({ name: "FindProduct" });
          break;
        case "สถานการณ์ราคาผลผลิต":
          this.$router.push({ name: "Market" });
          break;
        case "แนะนำผลผลิต":
          this.$router.push({ name: "Content", params: { id: "1" } });
          break;
        case "เกี่ยวกับเรา":
          this.$router.push({ name: "AboutUs" });
          break;
      }
      console.log(title);
    },
    logout() {
      localStorage.clear();
      this.$router.push({ name: "Login" });
    },
  },
  mounted() {
    this.loginItem = localStorage;
    console.log("storageData", this.loginItem);
  },
};
</script>
<style scoped>
</style>



