<template>
  <div>
    <v-container>
      <v-row no-gutters>
        <v-col :cols="12">
          <v-card-text
            class=""
            tile
            outlined
          >
            <v-card-title>
              <h3>หมวดสินค้าหลัก</h3>
            </v-card-title>
            <v-divider></v-divider>
            <div class="row">
              <v-flex
                md4
                class="lg5-custom pa-3"
                v-for="(item, index) in argiItems"
                :key="index"
              >
                <v-hover
                  v-slot:default="{ hover }"
                  open-delay="200"
                >
                  <v-card :elevation="hover ? 16 : 2">
                    <v-img
                      class="white--text align"
                      :src="require(`../assets/img/home/${item.nameTypeOfArgi_MK}.jpeg`)"
                      height="200px"
                    >
                    </v-img>

                    <v-card-text>
                      <v-row justify="center">
                        <h2 class="green--text"> {{ item.nameTypeOfArgi_MK }}</h2>
                      </v-row>
                    </v-card-text>

                    <div class="text-center">
                      <v-btn
                        :to="{ name: 'TypeList', params: { idTypeOfArgi: item.idTypeOfArgi, nameTypeOfArgi: item.nameTypeOfArgi_MK } }"
                        class="ma-2"
                        outlined
                        color="info"
                      >
                        ดูเพิ่มเติม
                      </v-btn>
                    </div>
                  </v-card>
                </v-hover>
              </v-flex>
            </div>
          </v-card-text>
        </v-col>
      </v-row>
      <div
        class="col-md-3 col-sm-6 col-xs-12"
        :key="index"
        v-for="(product, index) in filterImg"
      >
        <v-img
          height="200px"
          :src="product.imgTitle"
        >
        </v-img>
      </div>

    </v-container>

    <!-- <div class="light-green lighten-5 pa-5">
      <v-container>
        <p class="display-1 font-weight-light	text-center pa-4">แนะนำผลผลิต</p>
        <div class="row">
          <div
            class="col-md-4 col-sm-4 col-md-4"
            v-for="content in visibleContentPages"
            :key="content.id"
          >
            <v-hover v-slot="{ hover }">
              <v-card
                class="mx-auto"
                max-width="400"
                height="100%"
                :elevation="hover ? 16 : 2"
              >

                <v-img
                  class="white--text align-end"
                  height="300px"
                  max-width="400"
                  :src="content.contentImg"
                  @click="$router.push({name: 'Post', params: { id: content.contentId, page: 1}})"
                >
                </v-img>

                <v-card-title class="font-weight-bold">
                  {{ content.contentTitle }}
                </v-card-title>
              </v-card>
            </v-hover>
          </div>
        </div>
      </v-container>
    </div> -->

  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      baseUrl: "https://farmtd.hrdi.or.th/farmdd/admin/api/",
      productMk: [],
      page: 1,
      perPage: 6,
      contentType: [],
      contentData: [],
      newsContent: [],
      filterImg: [],
      productContent: [],
      argiItems: [],
      selection: 0,
    };
  },
  computed: {
    visiblePages() {
      return this.newsContent.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      );
    },
    visibleContentPages() {
      return this.productContent.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      );
    },
  },
  methods: {
    getTypeOfArgi() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/getAllTypeOfArgiTD.php"
        )
        .then((response) => {
          if (response.data.typeOfArgi.length > 0) {
            this.argiItems = response.data.typeOfArgi;
            this.argiItems = this.argiItems.filter((e, i) => {
              return e.nameTypeOfArgi_MK !== null;
            });
            console.log("getAllTypeOfArgiTD", this.argiItems);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    toThaiDateString(date) {
      let monthNames = [
        "มกราคม",
        "กุมภาพันธ์",
        "มีนาคม",
        "เมษายน",
        "พฤษภาคม",
        "มิถุนายน",
        "กรกฎาคม",
        "สิงหาคม.",
        "กันยายน",
        "ตุลาคม",
        "พฤศจิกายน",
        "ธันวาคม",
      ];

      let year = date.getFullYear() + 543;
      let month = monthNames[date.getMonth()];
      let numOfDay = date.getDate();

      let hour = date.getHours().toString().padStart(2, "0");
      let minutes = date.getMinutes().toString().padStart(2, "0");
      let second = date.getSeconds().toString().padStart(2, "0");

      return (
        `${numOfDay} ${month} ${year} ` + `${hour}:${minutes}:${second} น.`
      );
    },
  },
  mounted() {
    this.getTypeOfArgi();
  },
};
</script>



