  <v-container>
    <p class="display-1 indigo--text text--darken-4 font-weight-bold	text-center pt-4 mb-0"> {{ productMk.nameArgi }} </p>
    <p class="overline text-center">หมวด {{ productMk.nameTypeOfArgiMk }}</p>
    <v-divider></v-divider>
    <v-container
      fluid
      class="pt-0"
    >
      <v-row>
        <v-col
          class="d-flex child-flex pb-0"
          cols="6"
        >
          <v-img
            max-height="550"
            max-width="550"
            :src="productMk.titleImg"
            :lazy-src="productMk.imgTitle"
          >
            <template v-slot:placeholder>
              <v-row
                class="fill-height ma-0"
                align="center"
                justify="center"
              >
                <v-progress-circular
                  indeterminate
                  color="grey lighten-5"
                ></v-progress-circular>
              </v-row>
            </template>
          </v-img>
        </v-col>
        <v-col cols="6">
          <v-row>
            <h2 class="indigo--text text--darken-4 mx-auto">เดือนที่มีสินค้า</h2>
          </v-row>
          <v-row justify="center">
            <v-card flat>
              <v-card-text>
                <v-container
                  class="pa-0"
                  fluid
                >
                  <v-row
                    cols="12"
                    justify="center"
                  >
                    <v-col>
                      <h3>มค</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="1"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                    <v-col>
                      <h3>กพ</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="2"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                    <v-col>
                      <h3>มีค</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="3"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                    <v-col>
                      <h3>เมย</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="4"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                    <v-col>
                      <h3>พค</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="5"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                    <v-col>
                      <h3>มิ.ย.</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="6"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                  </v-row>
                  <v-row
                    cols="12"
                    justify="center"
                  >
                    <v-col>
                      <h3>กค</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="7"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                    <v-col>
                      <h3>สค</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="8"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                    <v-col>
                      <h3>กย</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="9"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                    <v-col>
                      <h3>ตค</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="10"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                    <v-col>
                      <h3>พย</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="11"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                    <v-col>
                      <h3>ธค</h3>
                      <v-checkbox
                        v-model="month"
                        color="green accent-4"
                        value="12"
                        hide-details
                        readonly
                      ></v-checkbox>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
            </v-card>
          </v-row>

          <v-divider></v-divider>
          <v-row class="mt-2">
            <h2 class="indigo--text text--darken-4 mx-auto">รายละเอียดสินค้า</h2>
          </v-row>
          <v-row
            class="pa-5 body-1"
            cols="12"
          >
            <v-col cols="12">
              <p class="body-1 font-weight-medium">{{ productMk.detailOfProduct }}</p>
            </v-col>
            <v-row
              cols="12"
              class="pa-2"
            >
              <v-col>
                <h3 class="indigo--text text--darken-4">สายพันธ์&nbsp;</h3>
                <h3 class="green--text text--accent-4">{{ productMk.speciesName || '-'}}</h3>
              </v-col>
              <v-col>
                <h3 class="indigo--text text--darken-4">มาตรฐาน&nbsp;</h3>
                <h3 class="green--text text--accent-4">{{ productMk.codeGrade || '-'}}</h3>
              </v-col>
            </v-row>
            <v-col cols="12">
              <h3 class="indigo--text text--darken-4">ราคา&nbsp;</h3>
              <h2 class="green--text text--accent-4">{{ productMk.priceBegin === productMk.priceEnd ? `฿${productMk.priceEnd}` : 
                      `฿${productMk.priceBegin} - ฿${productMk.priceEnd}` }} / {{ productMk.nameUnit }}</h2>
            </v-col>
          </v-row>
          <v-divider></v-divider>
        </v-col>
        <v-row>
          <v-col
            v-for="(item, index) in productImgMk"
            :key="index"
            class="d-flex child-flex"
            cols="1"
          >
            <v-img
              :src="item.imgTitle"
              :lazy-src="item.imgTitle"
            >
              <template v-slot:placeholder>
                <v-row
                  class="fill-height ma-0"
                  align="center"
                  justify="center"
                >
                  <v-progress-circular
                    indeterminate
                    color="grey lighten-5"
                  ></v-progress-circular>
                </v-row>
              </template>
            </v-img>
          </v-col>
        </v-row>
        <v-row class="mt-2">
          <h2 class="indigo--text text--darken-4 mx-auto">จัดจำหน่ายโดย</h2>
          <v-col cols="12">
            <p class="body-1 font-weight-medium"><span class="font-weight-bold">ประเภทองค์กร :</span> {{ sellerUser.nameTypeOfSeller }}</p>
            <p class="body-1 font-weight-medium"><span class="font-weight-bold">ชื่อองค์กร :</span> {{ sellerUser.groupName || '-' }}</p>
            <p class="body-1 font-weight-medium"><span class="font-weight-bold">ชื่อ :</span> {{ `${sellerUser.firstName} ${sellerUser.lastName}`}}</p>
            <p class="body-1 font-weight-medium"><span class="font-weight-bold">ที่อยู่ :</span> บ้านเลขที่ {{ sellerUser.address || '-'}}
              หมู่ {{ sellerUser.moo || '-'}} ตำบล {{ sellerUser.tamName || '-'}} จังหวัด {{ sellerUser.provName || '-'}} {{ sellerUser.postCode || ' '}} </p>
            <p class="body-1 font-weight-medium"><span class="font-weight-bold">พื้นที่โครงการ :</span> {{ area.areaName }}</p>
            <v-row cols="12">
              <v-col>
                <v-img
                  width="24"
                  :src="require('../assets/img/facebook.png')"
                ></v-img>
                <p class="body-1 font-weight-medium">{{ sellerUser.facebookName || '-' }}</p>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-row>
    </v-container>
  </v-container>
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      month: [],
      baseUrl: "https://farmtd.hrdi.or.th/farmdd/admin/api/",
      productMk: [],
      productImgMk: [],
      sellerUser: [],
      area: {},
      areaText: "",
    };
  },
  methods: {
    getProductImg(idProductOfMemberMk) {
      let formData = new FormData();
      formData.append("idProductOfMemberMk", idProductOfMemberMk);
      axios
        .post(`${this.baseUrl}/product/getProductImg.php`, formData)
        .then((res) => {
          console.log(res);
          if (res.data.productImgMk !== 0) {
            this.productImgMk = res.data.productImgMk;
            this.productImgMk.shift();
            console.log("productImgMk", this.productImgMk);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getProductDetail(idProductOfMemberMk) {
      let formData = new FormData();
      formData.append("idProductOfMemberMk", idProductOfMemberMk);
      axios
        .post(`${this.baseUrl}/product/getProductDetail.php`, formData)
        .then((res) => {
          if (res.data.productMk !== 0) {
            this.productMk = res.data.productMk[0];
            this.month = this.productMk.monthOfProduct.split(",");
            console.log(this.month);
            console.log("productMk", this.productMk);
          }
        })
        .then(() => {
          this.getSellerInfo();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getSellerInfo() {
      console.log(this.productMk.idSellerMember);
      let formData = new FormData();
      formData.append("idSellerMember", this.productMk.idSellerMember);
      axios
        .post(`${this.baseUrl}/product/postFindSellerMk.php`, formData)
        .then((res) => {
          if (res.data.sellerUser !== 0) {
            this.sellerUser = res.data.sellerUser[0];
            console.log("sellerUser", this.sellerUser);
          }
        })
        .then(() => {
          this.getAllArea();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getAllArea() {
      axios
        .get(`${this.baseUrl}/getAllArea.php`)
        .then((res) => {
          if (res.data.area !== 0) {
            let filter = res.data.area.filter((e, i) => {
              return e.idArea === this.sellerUser.vLinkAreaDetailId_MK;
            });
            this.area = {
              idArea: filter[0].areaName,
              areaName: filter[0].areaName,
            };
            console.log("area", this.area);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    let queryId = localStorage.idProductOfMemberMk;
    this.getProductDetail(queryId);
    this.getProductImg(queryId);
  },
  created() {},
};
</script>



