      <div class="row">
        <div
          class="col-md-4 col-sm-4 col-md-4"
          v-for="content in visibleContentPages"
          :key="content.id"
        >
          <v-hover v-slot="{ hover }">
            <v-card
              class="mx-auto"
              max-width="400"
              height="100%"
              :elevation="hover ? 16 : 2"
            >

              <v-img
                class="white--text align-end"
                height="300px"
                max-width="400"
                :src="content.contentImg"
                @click="$router.push({name: 'Post', params: { id: content.contentId, page: 1}})"
              >
              </v-img>
              <v-card-title class="font-weight-bold">
                {{ content.contentTitle }}
              </v-card-title>

              <v-card-actions>
                <v-btn
                  block
                  color="primary"
                  @click="$router.push({name: 'Post', params: { id: content.contentId, page: 1}})"
                >
                  ดูเพิ่มเติม
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-hover>
        </div>
      </div>
      <div class="text-center mb-5">
        <v-pagination
          v-model="page"
          :length="Math.ceil(contentData.length/perPage)"
        ></v-pagination>
      </div>
    </v-container>

