  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      page: 1,
      perPage: 6,
      contentType: [],
      contentData: [],
      productContent: [],
      selection: 0,
    };
  },
  computed: {
    visibleContentPages() {
      return this.contentData.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      );
    },
  },
  methods: {
    toThaiDateString(date) {
      let monthNames = [
        "มกราคม",
        "กุมภาพันธ์",
        "มีนาคม",
        "เมษายน",
        "พฤษภาคม",
        "มิถุนายน",
        "กรกฎาคม",
        "สิงหาคม.",
        "กันยายน",
        "ตุลาคม",
        "พฤศจิกายน",
        "ธันวาคม",
      ];

      let year = date.getFullYear() + 543;
      let month = monthNames[date.getMonth()];
      let numOfDay = date.getDate();

      let hour = date.getHours().toString().padStart(2, "0");
      let minutes = date.getMinutes().toString().padStart(2, "0");
      let second = date.getSeconds().toString().padStart(2, "0");

      return (
        `${numOfDay} ${month} ${year} ` + `${hour}:${minutes}:${second} น.`
      );
    },
  },
  mounted() {
    axios
      .get("https://farmtd.hrdi.or.th/farmdd/admin/api/content/getContent.php")
      .then((response) => {
        if (response.data.content.length > 0) {
          const onlyPublish = response.data.content.filter((c) => {
            return c.contentPublish !== "0";
          });
          this.contentData = onlyPublish;
          onlyPublish.map((content, index) => {
            this.contentData[
              index
            ].contentImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/${content.contentImg}`;
          });
          this.contentData = this.contentData.filter(e => e.contentTypeTitle === 'แนะนำผลผลิต')
          this.contentData.reverse();
          console.log(this.contentData);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>



