<template>
  <div>
    <v-carousel cycle>
      <v-carousel-item
        v-for="(img,i) in slideShowImg"
        :key="i"
        :src="img.imgTitle"
        reverse-transition="fade-transition"
        transition="fade-transition"
      ></v-carousel-item>
    </v-carousel>

    <div class="light-green lighten-5 pa-5">
      <v-container>
        <v-row no-gutters>
          <v-col :cols="12">
            <v-card-text
              class=""
              tile
              outlined
            >
              <v-card-title>
                <h3>สินค้าใหม่</h3>
              </v-card-title>
              <v-divider></v-divider>
              <div class="row">
                <v-flex
                  md3
                  class="lg5-custom pa-3"
                  v-for="news in newsProduct"
                  :key="news.idProductOfMemberMk"
                >
                  <v-hover
                    v-slot:default="{ hover }"
                    open-delay="200"
                  >
                    <v-card
                      :elevation="hover ? 16 : 2"
                      height="100%"
                    >
                      <v-img
                        class="white--text align"
                        :src="news.titleImg"
                        height="200px"
                      >
                      </v-img>

                      <v-card-text>
                        <v-row justify="center">
                          <h4 class="text-center grey--text text--darken-4"> {{ news.nameArgi }}</h4>
                        </v-row>
                      </v-card-text>

                      <div class="text-center">
                        <v-btn
                          @click="productDetail(news.idProductOfMemberMk)"
                          class="ma-2"
                          outlined
                          color="info"
                        >
                          ดูรายละเอียด
                        </v-btn>
                      </div>
                    </v-card>
                  </v-hover>
                </v-flex>
              </div>
            </v-card-text>
          </v-col>
        </v-row>
      </v-container>
    </div>

    <div>
      <v-container>
        <v-row no-gutters>
          <v-col :cols="12">
            <v-card-text
              class=""
              tile
              outlined
            >
              <v-card-title>
                <h3>สินค้าแนะนำ</h3>
              </v-card-title>
              <v-divider></v-divider>
              <div class="row">
                <v-flex
                  md3
                  class="lg5-custom pa-3"
                  v-for="product in visibleNewsProductPages"
                  :key="product.idProductOfMemberMk"
                >
                  <v-hover
                    v-slot:default="{ hover }"
                    open-delay="200"
                  >
                    <v-card
                      :elevation="hover ? 16 : 2"
                      height="100%"
                    >
                      <v-img
                        class="white--text align"
                        :src="product.titleImg"
                        height="200px"
                      >
                      </v-img>

                      <v-card-text>
                        <v-row justify="center">
                          <h4 class="text-center grey--text text--darken-4"> {{ product.nameArgi }}</h4>
                        </v-row>
                      </v-card-text>

                      <div class="text-center">
                        <v-btn
                          @click="productDetail(product.idProductOfMemberMk)"
                          class="ma-2"
                          outlined
                          color="info"
                        >
                          ดูรายละเอียด
                        </v-btn>
                      </div>
                    </v-card>
                  </v-hover>
                </v-flex>
              </div>
            </v-card-text>
          </v-col>
        </v-row>
        <div class="text-center mb-3">
          <v-pagination
            v-model="sugProductPage"
            :length="Math.ceil(suggestProduct.length/perPageNews)"
          ></v-pagination>
        </div>
      </v-container>
    </div>

    <div class="light-green lighten-5 pa-5 mt-5">
      <v-container>
        <v-row no-gutters>
          <v-col :cols="12">
            <v-card-text
              class=""
              tile
              outlined
            >
              <v-card-title>
                <h3>หมวดสินค้าหลัก</h3>
              </v-card-title>
              <v-divider></v-divider>
              <div class="row">
                <v-flex
                  md4
                  class="lg5-custom pa-3"
                  v-for="(item, index) in argiItems"
                  :key="index"
                >
                  <v-hover
                    v-slot:default="{ hover }"
                    open-delay="200"
                  >
                    <v-card :elevation="hover ? 16 : 2">
                      <v-img
                        class="white--text align"
                        :src="require(`../assets/img/home/${item.nameTypeOfArgi_MK}.jpeg`)"
                        height="200px"
                      >
                      </v-img>

                      <v-card-text>
                        <v-row justify="center">
                          <h2 class="green--text"> {{ item.nameTypeOfArgi_MK }}</h2>
                        </v-row>
                      </v-card-text>

                      <div class="text-center">
                        <v-btn
                          :to="{ name: 'TypeList', params: { idTypeOfArgi: item.idTypeOfArgi, nameTypeOfArgi: item.nameTypeOfArgi_MK } }"
                          class="ma-2"
                          outlined
                          color="info"
                        >
                          ดูเพิ่มเติม
                        </v-btn>
                      </div>
                    </v-card>
                  </v-hover>
                </v-flex>
              </div>
            </v-card-text>
          </v-col>
        </v-row>
      </v-container>
    </div>

    <v-container>
      <v-row no-gutters>
        <v-col :cols="12">
          <v-card-text
            class=""
            tile
            outlined
          >
            <v-card-title>
              <h3>ข่าวประชาสัมพันธ์</h3>
            </v-card-title>
            <v-divider></v-divider>
            <div class="row">
              <v-flex
                md4
                class="lg5-custom pa-3"
                v-for="content in visibleNewsPages"
                :key="content.contentId"
              >
                <v-hover
                  v-slot:default="{ hover }"
                  open-delay="200"
                >
                  <v-card
                    :elevation="hover ? 16 : 2"
                    height="100%"
                  >
                    <v-img
                      class="white--text align"
                      :src="content.contentImg"
                      height="200px"
                    >
                    </v-img>

                    <v-card-text>
                      <v-row justify="center">
                        <h4 class="text-center grey--text text--darken-4"> {{ content.contentTitle }}</h4>
                      </v-row>
                    </v-card-text>

                    <div class="text-center">
                      <v-btn
                        :to="{ name: 'Post', params: { id: content.contentId } }"
                        class="ma-2"
                        outlined
                        color="info"
                      >
                        อ่านเพิ่มเติม
                      </v-btn>
                    </div>
                  </v-card>
                </v-hover>
              </v-flex>
            </div>
          </v-card-text>
        </v-col>
      </v-row>
    </v-container>

    <div class="text-center mb-3">
      <v-pagination
        v-model="newsPage"
        :length="Math.ceil(newsContent.length/perPage)"
      ></v-pagination>
    </div>

    <div class="light-green lighten-5 pa-5 mt-5">
      <v-container>
        <v-row no-gutters>
          <v-col :cols="12">
            <v-card-text
              class=""
              tile
              outlined
            >
              <v-card-title>
                <h3>แนะนำผลผลิต</h3>
              </v-card-title>
              <v-divider></v-divider>
              <div class="row">
                <v-flex
                  md4
                  class="lg5-custom pa-3"
                  v-for="content in visiblePages"
                  :key="content.contentId"
                >
                  <v-hover
                    v-slot:default="{ hover }"
                    open-delay="200"
                  >
                    <v-card
                      :elevation="hover ? 16 : 2"
                      height="100%"
                    >
                      <v-img
                        class="white--text align"
                        :src="content.contentImg"
                        height="200px"
                      >
                      </v-img>

                      <v-card-text>
                        <v-row justify="center">
                          <h4 class="text-center grey--text text--darken-4"> {{ content.contentTitle }}</h4>
                        </v-row>
                      </v-card-text>

                      <div class="text-center">
                        <v-btn
                          :to="{ name: 'Post', params: { id: content.contentId } }"
                          class="ma-2"
                          outlined
                          color="info"
                        >
                          อ่านเพิ่มเติม
                        </v-btn>
                      </div>
                    </v-card>
                  </v-hover>
                </v-flex>
              </div>
            </v-card-text>
          </v-col>
        </v-row>
      </v-container>
      <div class="text-center mb-3">
        <v-pagination
          v-model="page"
          :length="Math.ceil(contentsData.length/perPage)"
        ></v-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      newsPage: 1,
      page: 1,
      sugProductPage: 1,
      perPageNews: 8,
      perPage: 3,
      slideShowImg: [],
      contentsData: [],
      newsContent: [],
      newsProduct: [],
      suggestProduct: [],
      items: [
        { title: "Click Me" },
        { title: "Click Me" },
        { title: "Click Me" },
        { title: "Click Me 2" },
      ],
      argiItems: [],
      activeBtn: 1,
      colors: [
        "indigo",
        "warning",
        "pink darken-2",
        "red lighten-1",
        "deep-purple accent-4",
      ],
      slides: ["First", "Second", "Third", "Fourth", "Fifth"],
    };
  },
  computed: {
    visiblePages() {
      return this.contentsData.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      );
    },
    visibleNewsPages() {
      return this.newsContent.slice(
        (this.newsPage - 1) * this.perPage,
        this.newsPage * this.perPage
      );
    },
    visibleNewsProductPages() {
      return this.suggestProduct.slice(
        (this.sugProductPage - 1) * this.perPageNews,
        this.sugProductPage * this.perPageNews
      );
    },
  },
  methods: {
    productDetail(idProduct) {
      this.$router.push({
        name: "ProductDetail",
        params: { idProductOfMemberMk: idProduct },
      });
    },
    getContentFirstPage() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/content/getContentFirstPage.php"
        )
        .then((response) => {
          if (response.data.content.length > 0) {
            console.log(response.data);
            this.contentsData = response.data.content;
            this.contentsData = this.contentsData.filter((e, i) => {
              return (
                e.contentTypeTitle === "แนะนำผลผลิต" && e.contentPublish === "1"
              );
            });
            this.contentsData.map(
              (e) =>
                (e.contentImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/${e.contentImg}`)
            );
            this.contentsData.reverse();
            console.log("getContentFirstPage", this.contentsData);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getContent() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/content/getContent.php"
        )
        .then((response) => {
          if (response.data.content.length > 0) {
            this.newsContent = response.data.content.filter(
              (e) =>
                e.contentTypeTitle === "ข่าวประชาสัมพันธ์" &&
                e.contentPublish === "1"
            );
          }
          this.newsContent.map((e, i) => {
            e.contentImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/${e.contentImg}`;
          });

          this.newsContent.reverse();
          console.log("newsContent", this.newsContent);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getTypeOfArgi() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/getAllTypeOfArgiTD.php"
        )
        .then((response) => {
          if (response.data.typeOfArgi.length > 0) {
            this.argiItems = response.data.typeOfArgi;
            this.argiItems = this.argiItems.filter((e, i) => {
              return e.nameTypeOfArgi_MK !== null;
            });
            console.log("getAllTypeOfArgiTD", this.argiItems);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getNewProduct() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/product/getNewProduct.php"
        )
        .then((res) => {
          if (res.data.productMk !== 0) {
            this.newsProduct = res.data.productMk;
            console.log("newsProduct", this.newsProduct);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getSuggestProduct() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/product/getSuggestProduct.php"
        )
        .then((res) => {
          if (res.data.suggestProduct !== 0) {
            this.suggestProduct = res.data.suggestProduct;
            console.log("suggestProduct", this.suggestProduct);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getSlideShowImg() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/admin/api/slideshow/getAllSlideShow.php")
        .then((res) => {
          this.slideShowImg = res.data.slideShowImg;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.getContent();
    this.getContentFirstPage();
    this.getTypeOfArgi();
    this.getNewProduct();
    this.getSuggestProduct();
    this.getSlideShowImg();
  },
};
</script>
<style>
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: 0.5;
  position: absolute;
  width: 100%;
}

.card-actions {
  margin: 10px;
  position: absolute;
  top: 0;
}

.button-actions {
  border-radius: 0px;
  position: absolute;
  bottom: 0;
}
</style>




