<script>
import axios from "axios";

export default {
  data() {
    return {
      paramsId: 0,
      error: {
        isError: false,
        errorMsg: "",
      },
      uri: "",
      marketName: "",
      marketType: "",
      showCard: {
        showVega: false,
        showMeat: false,
      },
      isTaiMarket: false,
      taiFunction: false,
      isCornerMarket: false,
      isSriMarket: false,
      cornerFunction: false,
      isLoad: false,
      product: [],
      search: "",
      vegaSelect: [
        "ตลาดผลไม้ฤดูกาล",
        "ตลาดผลไม้รวม",
        "ตลาดส้ม",
        "ตลาดแตงโม",
        "ลานผัก",
        "ตลาดผลไม้นานาชาติ และตลาดคอนเทนเนอร์",
        "ลานผัก (โซนผักปลีก)",
        "ตลาดผัก",
      ],
      meatSelect: ["ตลาดสด", "ตลาดปลาน้ำจืด", "ตลาดอาหารทะเล"],
      riceSelect: ["ตลาดข้าวสาร"],
      fieldSelect: ["ตลาดพืซไร่"],
      dryFoodSelect: ["ตลาดอาหารแห้ง", "ตลาดสินค้าเบ็ดเตล็ด"],
      plantSelect: ["ตลาดต้นไม้", "ตลาดดอกไม้", "ตลาดสัตว์เลี้ยง"],
      headerProps: {
        sortByText: "Custom sort by",
      },
      headers: [],
    };
  },
  methods: {
    getColor(dif) {
      if (dif.charAt(0) === "-") {
        return "red";
      } else if (dif.substring(0, 4) === "0.00") {
        return "grey";
      } else {
        return "green";
      }
    },
    checkDif(dif) {
      if (dif.charAt(0) === "-") {
        return "mdi-arrow-down";
      } else if (dif.substring(0, 4) === "0.00") {
        return "mdi-minus";
      } else {
        return "mdi-arrow-up";
      }
    },
    getProductData(uri, marketName) {
      this.product = [];
      this.headers = [
        { text: "ชื่อสินค้า", value: "post_title" },
        { text: "ราคา / กิโลกรัม", value: "range_value" },
        { text: "ราคาเพิ่ม / ลด", value: "priceDif" },
      ];
      this.uri = uri;
      this.marketName = marketName;
      if (this.uri !== "") {
        this.isTaiMarket = false;
        this.showCard.showVega = false;
        this.isCornerMarket = false;
        this.taiFunction = true;
        this.cornerFunction = false;
        axios
          .get(this.uri)
          .then((response) => {
            this.product = response.data.posts;
            response.data.posts.map((data, index) => {
              this.product[index].post_title = data.post_title.substring(10);
              this.product[index].range_value = this.product[
                index
              ].acf.recent_price.low_price
                .concat(" - ")
                .concat(
                  this.product[index].acf.recent_price.high_price.concat(" บาท")
                );
              this.product[index].priceDif = (
                data.acf.recent_price.difference * 100
              )
                .toFixed(2)
                .concat(" %");
            });
            this.isLoad = false;
            console.log(this.product);
          })
          .catch((err) => {
            if (err) this.isLoad = false;
          });
      }
    },
    showTable() {
      this.isLoad = true;

      switch (this.marketName) {
        case "ตลาดผลไม้ฤดูกาล":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7324&per_page=500",
            "ตลาดไท > ตลาดผลไม้ฤดูกาล"
          );
          break;
        case "ตลาดผลไม้รวม":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7326&per_page=500",
            "ตลาดไท > ตลาดผลไม้รวม"
          );
          break;
        case "ตลาดส้ม":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7331&per_page=500",
            "ตลาดไท > ตลาดส้ม"
          );
          break;
        case "ตลาดแตงโม":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7334&per_page=500",
            "ตลาดไท > ตลาดแตงโม"
          );
          break;
        case "ลานผัก":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=4951&per_page=500",
            "ตลาดไท > ลานผัก"
          );
          break;
        case "ตลาดผลไม้นานาชาติ และตลาดคอนเทนเนอร์":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7318&per_page=500",
            "ตลาดไท > ตลาดผลไม้นานาชาติ และตลาดคอนเทนเนอร์"
          );
          break;
        case "ลานผัก (โซนผักปลีก)":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7306&per_page=500",
            "ลานผัก (โซนผักปลีก)"
          );
          break;
        case "ตลาดผัก":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7310&per_page=500",
            "ตลาดผัก"
          );
          break;
        case "ตลาดสด":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=10132&per_page=500",
            "ตลาดสด"
          );
          break;
        case "ตลาดปลาน้ำจืด":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=10133&per_page=500",
            "ตลาดปลาน้ำจืด"
          );
          break;
        case "ตลาดอาหารทะเล":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=10134&per_page=500",
            "ตลาดอาหารทะเล"
          );
          break;
        case "ตลาดข้าวสาร":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7353&per_page=500",
            "ตลาดข้าวสาร"
          );
          break;
        case "ตลาดข้าวสาร":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7324&per_page=500",
            "ตลาดข้าวสาร"
          );
          break;
        case "ตลาดพืซไร่":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7364&per_page=500",
            "ตลาดพืซไร่"
          );
          break;
        case "ตลาดอาหารแห้ง":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=10135&per_page=500",
            "ตลาดอาหารแห้ง"
          );
          break;
        case "ตลาดสินค้าเบ็ดเตล็ด":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=10136&per_page=500",
            "ตลาดสินค้าเบ็ดเตล็ด"
          );
          break;
        case "ตลาดต้นไม้":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7375&per_page=500",
            "ตลาดต้นไม้"
          );
          break;
        case "ตลาดดอกไม้":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7398&per_page=500",
            "ตลาดดอกไม้"
          );
          break;
        case "ตลาดสัตว์เลี้ยง":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=10130&per_page=500",
            "ตลาดสัตว์เลี้ยง"
          );
          break;
      }
    },
    showCornerTable(cornerName) {
      this.cornerFunction = true;
      this.isLoad = true;
      this.isCornerMarket = false;
      this.taiFunction = false;
      this.product = [];
      this.headers = [
        { text: "รูปสินค้า", value: "img" },
        { text: "ราคาสินค้าวันที่", value: "price_date" },
        { text: "ชื่อสินค้า", value: "post_title" },
        { text: "ใหญ่", value: "priceL" },
        { text: "กลาง", value: "priceM" },
        { text: "เล็ก", value: "priceS" },
      ];
      if (cornerName === "ผัก") {
        const payload = {
          fromRow: 1,
          selectedMarket: [],
          selectedProductGroup: [1],
          sortOrder: "PriceL desc",
          toRow: 500,
        };
        this.getCornerData(payload);
      } else if (cornerName === "ผลไม้") {
        const payload = {
          fromRow: 1,
          selectedMarket: [],
          selectedProductGroup: [2],
          sortOrder: "PriceL desc",
          toRow: 500,
        };
        this.getCornerData(payload);
      } else if (cornerName === "พืซไร่") {
        const payload = {
          fromRow: 1,
          selectedMarket: [],
          selectedProductGroup: [3],
          sortOrder: "PriceL desc",
          toRow: 500,
        };
        this.getCornerData(payload);
      } else if (cornerName === "ของแห้งของดอง") {
        const payload = {
          fromRow: 1,
          selectedMarket: [],
          selectedProductGroup: [4],
          sortOrder: "PriceL desc",
          toRow: 500,
        };
        this.getCornerData(payload);
      } else if (cornerName === "ของสดของชำ") {
        const payload = {
          fromRow: 1,
          selectedMarket: [],
          selectedProductGroup: [5],
          sortOrder: "PriceL desc",
          toRow: 500,
        };
        this.getCornerData(payload);
      } else if (cornerName === "ดอกไม้สังฆภัณฑ์") {
        const payload = {
          fromRow: 1,
          selectedMarket: [],
          selectedProductGroup: [6],
          sortOrder: "PriceL desc",
          toRow: 500,
        };
        this.getCornerData(payload);
      } else {
        return;
      }
    },
    getCornerData(payload) {
      axios
        .post(
          "https://www.simummuangmarket.com/api/product/frontend/search",
          payload
        )
        .then((res) => {
          this.product = res.data.data;
          res.data.data.map((data, index) => {
            this.product[index].post_title = data.ProductNameTH;
            if (data.PriceL != null) {
              this.product[index].priceL =
                data.PriceL + "".concat(` Bath / ${data.UnitNameTH}`);
            } else {
              this.product[index].priceL = "-";
            }

            if (data.PriceM != null) {
              this.product[index].priceM =
                data.PriceM + "".concat(` Bath / ${data.UnitNameTH}`);
            } else {
              this.product[index].priceM = "-";
            }

            if (data.PriceS != null) {
              this.product[index].priceS =
                data.PriceS + "".concat(` Bath / ${data.UnitNameTH}`);
            } else {
              this.product[index].priceS = "-";
            }

            if (data.PriceDate != null) {
              this.product[index].price_date = data.PriceDate.replace(/T.*/, "")
                .split("-")
                .reverse()
                .join("/");
            } else {
              this.product[index].price_date = "-";
            }

            if (data.LargeImageUrl === null) {
              this.product[index].img =
                "https://www.simummuangmarket.com/img/img-default.png";
            } else {
              this.product[
                index
              ].img = `https://www.simummuangmarket.com${data.LargeImageUrl}`;
            }
          });
          console.log(this.product);
          this.isLoad = false;
        })
        .catch((err) => {
          if (err) {
            this.isLoad = false;
          }
        });
    },
    showSriTable(market) {
      this.cornerFunction = false;
      this.isLoad = true;
      this.isSriMarket = false;
      this.taiFunction = false;
      this.product = [];
      this.headers = [
        { text: "ชื่อสินค้า", value: "post_title" },
        { text: "ราคาสินค้า", value: "price" },
        { text: "หน่วยสินค้า", value: "priceDesc" },
      ];

      switch (market) {
        case "ผัก":
          this.paramsId = 1;
          break;
        case "ผักพืซไร่เมืองหนาว":
          this.paramsId = 2;
          break;
        case "ผลไม้":
          this.paramsId = 3;
          break;
        case "ผลไม้พืซไร่":
          this.paramsId = 4;
          break;
        case "ไม้ดอกไม้ประดับ":
          this.paramsId = 5;
          break;
      }

      axios
        .get(`https://hrdi-api.herokuapp.com/vegatable/${this.paramsId}`)
        .then((res) => {
          console.log(res.data);
          this.product = res.data;
          this.product = [
            ...res.data.filter(
              (x) =>
                !x.includes("00") &&
                x !== "กิโลกรัม" &&
                x !== "กำ" &&
                x !== "หัว" &&
                x !== "ลูก" &&
                x !== "ร้อยละ" &&
                x !== "แพ็ค" &&
                x !== "หวี" &&
                x !== "กล่อง"
            ),
          ].map((value, index) => ({ post_title: value }));
          res.data
            .filter((x) => x.includes("00"))
            .map((value, index) => {
              this.product[index].price = value;
            });
          res.data
            .filter(
              (x) =>
                (!x.includes("00") && x === "กิโลกรัม") ||
                x === "กำ" ||
                x === "หัว" ||
                x === "ลูก" ||
                x === "ร้อยละ" ||
                x === "ร้อยละ" ||
                x === "แพ็ค" ||
                x === "หวี" ||
                x === "กล่อง"
            )
            .map((value, index) => {
              this.product[index].priceDesc = value;
            });
          this.isLoad = false;
        });
    },
    toggleCard(isMarket) {
      switch (isMarket) {
        case "isTaiMarket":
          this.isTaiMarket = !this.isTaiMarket;
          this.isCornerMarket = false;
          this.isSriMarket = false;
          break;
        case "isCornerMarket":
          this.isTaiMarket = false;
          this.isCornerMarket = !this.isCornerMarket;
          this.isSriMarket = false;
          break;
        case "isSriMarket":
          this.isTaiMarket = false;
          this.isCornerMarket = false;
          this.isSriMarket = !this.isSriMarket;
          break;
      }
    },
  },
  created() {},
};
</script>

<style>
.pointer {
  cursor: pointer;
}

.pointer:hover {
  color: green;
}
</style>



