<template>
  <v-container>
    <p
      class="display-3 font-weight-bold text-center pa-4"
      style="color: #074d66;"
    >{{ aboutContent.aboutTitle }}</p>
    <v-container fluid>
      <v-row>
        <v-col
          class="content"
          md="12"
          lg="12"
          v-html="aboutContent.aboutBody"
        >
        </v-col>
      </v-row>
    </v-container>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      aboutContent: [],
    };
  },
  methods: {
    getAboutUs() {
      axios
        .get(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/aboutus/getAboutUs.php"
        )
        .then((res) => {
          this.aboutContent = res.data.aboutus[0];
          console.log(this.aboutContent);
        });
    },
  },
  mounted() {
    this.getAboutUs();
  },
};
</script>
<style>
img .img-responsive {
  max-width: 100%;
  height: auto;
}

.img-responsive {
  display: block;
  max-width: 100% !important;
  height: auto;
}

table {
  max-width: 100% !important;
  overflow-x: auto !important;
}
</style>


