                          value="ไม้ดอกไม้ประดับ"
                        ></v-checkbox>
                        <v-checkbox
                          v-model="sellerForm.productType"
                          label="ผลิตภัณฑ์แปรรูป"
                          value="ผลิตภัณฑ์แปรรูป"
                        ></v-checkbox>
                        <v-checkbox
                          v-model="sellerForm.productType"
                          label="ชา กาแฟ และโกโก้"
                          value="ชา กาแฟ และโกโก้"
                        ></v-checkbox>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                      >
                        <v-checkbox
                          v-model="sellerForm.productType"
                          label="หัตถกรรม"
                          value="หัตถกรรม"
                        ></v-checkbox>
                        <v-checkbox
                          v-model="sellerForm.productType"
                          label="พืซท้องถิ่น"
                          value="พืซท้องถิ่น"
                        ></v-checkbox>
                        <v-checkbox
                          v-model="sellerForm.productType"
                          label="ปศุสัตว์ และประมง"
                          value="ปศุสัตว์ และประมง"
                        ></v-checkbox>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-col>
              </v-card-text>
              <v-card-actions>
                <v-btn
                  block
                  color="primary"
                  @click="checkSellerRegister"
                  :disabled="!isFormValid"
                >ยืนยันสมัครสมาชิก</v-btn>
              </v-card-actions>
            </v-card>
          </v-form>
        </v-row>
        <!-- End Seller Form !-->
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    imagePreviewURL: null,
    isFormValid: false,
    confirmPassword: "",
    buyerForm: {
      prefix: "",
      firstName: "",
      lastName: "",
      username: "",
      phoneNumber: "",
      email: "",
      idCard: "",
      password: "",
      img: [],
      provinceCode: "",
      typeOfMember: "",
      amphurCode: "",
      tambolCode: "",
      lineId: "",
      postcode: "",
      facebookName: "",
      address: "",
      moo: "",
      road: "",
      questionOfVisit: "",
      questionOfObjective: "",
    },
    sellerForm: {
      idTypeOfSeller: "",
      prefix: "",
      firstName: "",
      lastName: "",
      username: "",
      phoneNumber: "",
      email: "",
      idCard: "",
      gender: "",
      password: "",
      vProvinceId: "",
      vLinkAreaDetailId: "",
      productType: [],
      postcode: "",
      address: "",
      img: [],
      moo: "",
      road: "",
      tambolCode: "",
      ampCode: "",
      zipcode: "",
      groupName: "",
      shopName: "",
      brandName: "",
      lineId: "",
      facebookName: "",
    },
    isRegisForm: {
      buyer: false,
      seller1: false,
    },
    isAgree: "",
    valid: false,
    userType: "",
    isType: true,
    isShowPolicy: false,
    policy: {
      showBuyerPolicy: false,
      showSellerPolicy: false,
      title: "",
      titleColor: "",
      ruleTitle: "",
      firstRule: "",
      secRule: "",
      thirdRule: "",
      fourthRule: "",
      fifthRule: "",
      sixthRule: "",
      seventhRule: "",
      eigthRule: "",
      ninethRule: "",
      footer: "",
    },
    prefix: [],
    area: [],
    sellType: [],
    amphur: [],
    tambol: [],
    province: [
      {
        provinceName: "",
      },
    ],
    targetName: [],
    targetFilter: [],
    allProvince: [],
    buyerAmphur: [],
    buyerTambol: [],
    isUsernameExist: false,
    showEtc: {
      visit: false,
      objective: false,
    },
    firstNameRules: [(v) => !!v || "กรุณากรอกชื่อ"],
    lastNameRules: [(v) => !!v || "กรุณากรอกนามสกุล"],
    imgRules: [
      (value) => !!value || "กรุณาเลือกรูปภาพประจำตัว",
      (value) =>
        !value || value.size < 3000000 || "ขนาดรูปภาพไม่สามารถเกิน 3 MB!",
    ],
    emailRules: [(v) => /.+@.+/.test(v) || "อีเมลต้องอยู่ในรูปแบบที่ถูกต้อง"],
    phoneRules: [
      (v) => !v.length < 10 || "หมายเลขโทรศัพท์ต้องมีจำนวน 10 หลัก",
      (v) => !!v || "กรุณากรอกหมายเลขโทรศัพท์",
    ],
    passwordRules: [
      (v) => !!v || "กรุณากรอกรหัสผ่าน",
      (v) => /[A-Z]+/.test(v) || "ต้องมีตัวอักษรตัวใหญ่ในรหัสผ่าน",
      (v) => /[0-9]/g.test(v) || "ตัองมีตัวเลขในรหัสผ่าน",
      (v) => v.length >= 6 || "รหัสผ่านต้องมีอย่างน้อย 6 ตัว",
      (v) => /^[a-zA-Z0-9äöüÄÖÜ]*$/.test(v) || "ไม่อนุญาตให้ใช้ตัวอักษรพิเศษ",
      (v) =>
        (v || "").indexOf(" ") < 0 || "ไม่อนุญาตให้ใช้รหัสผ่านที่มีช่องว่าง",
    ],
  }),
  methods: {
    previewImg(payload) {
      const file = payload;
      this.sellerForm.img = file;
      this.buyerForm.img = file;
      if (file) {
        this.imagePreviewURL = URL.createObjectURL(file);
        URL.revokeObjectURL(file);
      } else {
        this.imagePreviewURL = null;
      }
    },
    findTumbolByAmpCode(ampId, provinceId) {
      this.tambol = [];
      axios
        .get(
          `https://farmtd.hrdi.or.th/farmdd/admin/api/getTumbolByAmpProv.php?provinceId=${provinceId}&ampId=${ampId}`
        )
        .then((res) => {
          if (res.data.tambol.length > 0) {
            this.tambol = res.data.tambol;
          }
          console.log("getTumbolByAmpProv", this.tambol);
        });
    },
    getAmphurByProvince(provinceId) {
      this.amphur = [];
      this.tambol = [];
      axios
        .get(
          `https://farmtd.hrdi.or.th/farmdd/admin/api/getAmphurById.php?provinceId=${provinceId}`
        )
        .then((res) => {
          if (res.data.amphur.length > 0) {
            this.amphur = res.data.amphur;
          }
          console.log("getAmphurById", this.amphur);
        });
    },
    questionBuyer(select) {
      if (select === "อื่น ๆ") {
        this.showEtc.visit = true;
      } else {
        this.showEtc.visit = false;
      }
    },
    checkUsername(username) {
      this.isUsernameExist = false;
      var usernameFormData = new FormData();
      usernameFormData.append("username", username);

      var userForm = {};
      usernameFormData.forEach(function (value, key) {
        userForm[key] = value;
      });
      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/checkUsername.php",
          JSON.stringify(userForm)
        )
        .then((res) => {
          if (res.data.username[0].username.length > 0) {
            this.isUsernameExist = true;
          }
        });
    },
    questionObjBuyer(select) {
      if (select === "อื่น ๆ") {
        this.showEtc.objective = true;
      } else {
        this.showEtc.objective = false;
      }
    },
    findAreaByProvince(provinceId) {
      this.getAmphurByProvince(provinceId);
      axios
        .get(
          `https://farmtd.hrdi.or.th/farmdd/admin/api/getVLinkTargetById.php?provinceId=${provinceId}`
        )
        .then((res) => {
          console.log("vTargetDetail", res.data.vTargetDetail);
          if (res.data.vTargetDetail.length > 0) {
            this.targetName = [
              ...new Map(
                res.data.vTargetDetail.map((item) => [item.targetName, item])
              ).values(),
            ];
            this.getArea();
          }
        });
    },
    getArea() {
      axios
        .get(`https://farmtd.hrdi.or.th/farmdd/admin/api/getAllArea.php`)
        .then((res) => {
          this.targetName = this.targetName.map((item) => item.targetId);
          console.log("targetName", this.targetName);
          if (res.data.area.length > 0) {
            this.area = res.data.area;
          }
          this.targetFilter = res.data.area.filter((item) =>
            this.targetName.includes(item.idArea)
          );
          console.log("targetFilter", this.targetFilter);
        });
    },
    checkLoginType(type) {
      this.isType = false;
      this.isShowPolicy = true;
      if (type === "Buyer") {
        this.policy = {
          showBuyerPolicy: true,
          showSellerPolicy: false,
          title: "ผู้ซื้อ",
          titleColor: "indigo--text",
          ruleTitle:
            "ผู้ขอสมัครสมาชิกซึ่งต่อไปนี้จะเรียกว่า ผู้สมัคร และเวบของดีบนดอยออนไลน์ ซึ่งอยู่ภายใต้การดูแลของสถาบันวิจัยและพัฒนาพื้นที่สูง ต่อไปนี้จะเรียกว่า สถาบันฯ กรุณาอ่านข้อตกลงด้านล่างนี้โดยละเอียด เพื่อรักษาสิทธิประโยชน์ในการเป็นสมาชิกของท่านตามเงื่อนไขดังนี้",
          firstRule:
            "1. การสมัครสมาชิก เวบของดีบนดอยออนไลน์ ไม่ต้องเสียค่าใช้จ่ายใดใดทั้งสิ้น",
          secRule:
            "2. ผู้สมัครจะต้องกรอกข้อมูลรายละเอียดต่างๆ ตามจริงให้ครบถ้วนเพื่อผลประโยชน์แก่ตัวผู้สมัคร หากตรวจพบว่า ข้อมูลของผู้สมัครไม่เป็นความจริง ทางสถาบันจะขอระงับการใช้งานของผู้สมัครโดยไม่ต้องแจ้งให้ทราบล่วงหน้า",
          thirdRule:
            "3. ผู้ใดแอบอ้างหรือการกระทำใดใด อันเป็นการละเมิดสิทธิส่วนบุคคล โดยใช้ข้อมูลของผู้อื่นมาแอบอ้างสมัครเป็นสมาชิก เพื่อให้ได้มาซึ่งการเป็นสมาชิก ถือเป็นความผิดต้องได้รับโทษตามกฏหมายที่กำหนดไว้",
          fourthRule:
            "4. ข้อมูลส่วนบุคคลของผู้สมัครที่ได้ลงทะเบียนหรือผ่านการใช้งานของเวบไซด์ ของดีบนดอยออนไลน์ ทั้งหมดนั้น ผู้สมัครยอมรับและตกลงว่าเป็นสิทธิของสถาบันฯ ซึ่งผู้สมัครต้องอนุญาตให้สถาบัน ใช้ข้อมูลของผู้สมัครสมาชิกในงานที่เกี่ยวข้องกับการส่งเสริมอาชีพแก่เกษตรกรบนพื้นที่สูง",
          fifthRule:
