        .then((res) => {
          this.prefix = res.data.prefix;
          console.log("prefix", this.prefix);
        });
    },
    getProvince() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/admin/api/getVLinkProvince.php")
        .then((res) => {
          if (res.data.vLinkProvince.length > 0) {
            this.province = [
              ...new Map(
                res.data.vLinkProvince.map((item) => [item.provinceId, item])
              ).values(),
            ];
          }

          this.province = this.province.filter((item, index) => {
            return (
              item.provinceName !== "เลย" &&
              item.provinceName !== "ลำปาง" &&
              item.provinceName !== "แพร่"
            );
          });

          console.log("province", this.province);
        });
    },
    seller2Revert() {
      this.isRegisForm = {
        seller1: true,
        seller2: false,
        seller3: false,
      };
    },
    getSellerType() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/admin/api/getSellerType.php")
        .then((res) => {
          this.sellType = res.data.sellType;
          console.log("sellType", this.sellType);
        });
    },
    getAllProvince() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/admin/api/getAllprovince.php")
        .then((res) => {
          if (res.data.allProvince.length > 0) {
            this.allProvince = res.data.allProvince;
          }
          console.log("getAllprovince", this.allProvince);
        });
    },
  }, // End Method
  mounted() {
    this.getPrefix();
    this.getProvince();
    this.getSellerType();
    this.getAllProvince();
  },
};
</script>
<style scoped>
.border {
  box-shadow: 0px 0px 0px 5px #2d3753 inset;
}
#rounded {
  border-radius: 5px;
}
</style>



