<template>
  <v-container
    fluid
    fill-height
  >
    <v-layout
      align-center
      justify-center
    >
      <v-flex
        sm12
        md9
        lg12
        class="pa-5"
      >
        <v-row cols="12">
          <v-col
            cols="12"
            v-if="showAmount"
          >
            <v-card
              class="mx-auto"
              tile
            >
              <v-toolbar
                color="primary"
                dark
              >
                <v-icon class="mr-2">mdi-barn</v-icon>
                <v-toolbar-title>สินค้าประเภท {{ nameTypeOfArgiMk }}</v-toolbar-title>
              </v-toolbar>

              <v-list>
                <v-list-item-group color="primary">
                  <v-list-item
                    v-for="(product) in productMk"
                    :key="product.idAgri"
                    @click="itemClick(product.idAgri, product.nameArgi)"
                  >
                    <v-list-item-content>
                      <v-list-item-title v-text="product.nameArgi"></v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-card>
          </v-col>

          <v-row
            md="12"
            v-if="showDetails"
          >
            <v-col cols="12">
              <l-map
                ref="productMap"
                style="z-index: 0; height: 500px; width: 100%"
                :zoom="zoom"
                :center="center"
                @update:zoom="zoomUpdated"
                @update:center="centerUpdated"
                @update:bounds="boundsUpdated"
              >
                <l-tile-layer :url="url"></l-tile-layer>
                <l-marker
                  v-for="marker in markers"
                  :key="marker.id"
                  :visible="marker.visible"
                  :lat-lng.sync="marker.position"
                  :icon="marker.icon"
                  @click="showProduct(marker.idSellerMember, marker.idAgri, marker.lat, marker.lng)"
                >
                  <!-- <l-popup
                    :content="marker.tooltip"
                  /> -->
                  <l-tooltip
                    :content="marker.tooltip"
                    :options="{ permanent: true }"
                  />
                </l-marker>
              </l-map>
            </v-col>
            <v-col cols="12">
              <v-card
                class="mx-auto"
                tile
              >
                <v-toolbar
                  color="primary"
                  dark
                >
                  <v-icon
                    class="mr-2"
                    @click="back"
                  >mdi-arrow-left-thick</v-icon>
                  <v-toolbar-title>รายชื่อศูนย์ [{{ nameOfArgi || '-' }}]</v-toolbar-title>
                </v-toolbar>

                <v-simple-table>
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th class="text-left font-weight-black indigo--text text--darken-3">
                          <h3>ชื่อกลุ่ม</h3>
                        </th>
                        <th class="text-left font-weight-black indigo--text text--darken-3">
                          <h3>จังหวัด</h3>
                        </th>
                        <th class="text-left font-weight-black indigo--text text--darken-3">
                          <h3>ชื่อศูนย์</h3>
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="(product, index) in productByArgiList"
                        :key="index"
                        @click="showProduct(product.idSellerMember, product.idAgri, parseFloat(product.lat), parseFloat(product.long))"
                        style="cursor: pointer;"
                      >
                        <td>{{ product.groupName || '-' }}</td>
                        <td>{{ product.provinceName }}</td>
                        <td>{{ product.areaName }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </v-card>
            </v-col>
          </v-row>

          <v-row
            cols="12"
            v-if="isShowListProduct"
          >
            <v-col cols="12">
              <l-map
                ref="productMap"
                style="z-index: 0; height: 500px; width: 100%"
                :zoom="zoom"
                :center="center"
                @update:zoom="zoomUpdated"
                @update:center="centerUpdated"
                @update:bounds="boundsUpdated"
              >
                <l-tile-layer :url="url"></l-tile-layer>
                <l-marker
                  v-for="marker in markers"
                  :key="marker.id"
                  :visible="marker.visible"
                  :lat-lng.sync="marker.position"
                  :icon="marker.icon"
                  @click="showProduct(marker.idSellerMember, marker.idAgri, marker.lat, marker.lng, marker.areaName)"
                >
                  <!-- <l-popup
                    :content="marker.tooltip"
                  /> -->
                  <l-tooltip
                    :content="marker.tooltip"
                    :options="{ permanent: true }"
                  />
                </l-marker>
              </l-map>
            </v-col>
            <v-col cols="12">
              <v-col>
                <v-card
                  class="mx-auto"
                  tile
                >
                  <v-toolbar
                    color="primary"
                    dark
                  >
                    <v-icon
                      class="mr-2"
                      @click="backFromArea()"
                    >mdi-arrow-left-thick</v-icon>
                    <v-toolbar-title>สินค้าภายในพื้นที่</v-toolbar-title>
                  </v-toolbar>

                  <v-simple-table class="pa-5">
                    <template v-slot:default>
                      <thead>
                        <tr>
                          <th class="text-left font-weight-black indigo--text text--darken-3">
                            <h3>รูปสินค้า</h3>
                          </th>
                          <th class="text-left font-weight-black indigo--text text--darken-3">
                            <h3>ชื่อสินค้า</h3>
                          </th>
                          <th class="text-left font-weight-black indigo--text text--darken-3">
                            <h3>สายพันธ์</h3>
                          </th>
                          <th class="text-left font-weight-black indigo--text text--darken-3">
                            <h3>รายละเอียดสินค้า</h3>
                          </th>
                          <th class="text-left font-weight-black indigo--text text--darken-3">
                            <h3>ราคา</h3>
                          </th>
                          <th class="text-left font-weight-black indigo--text text--darken-3">
                            <h3>ชื่อกลุ่ม</h3>
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr
                          v-for="(product, index) in productByArea"
                          :key="index"
                          @click="productDetail(product.idProductOfMemberMk)"
                          style="cursor: pointer;"
                        >
                          <td>
                            <v-img
                              width="100"
                              height="100"
                              :src="product.titleImg"
                              :lazy-src="product.titleImg"
                            ></v-img>
                          </td>
                          <td> {{ product.nameArgi }}</td>
                          <td>{{ product.speciesName || '-' }}</td>
                          <td>{{ product.detailOfProduct || '-' }}</td>
                          <td>{{ product.priceBegin === product.priceEnd ? `฿${product.priceEnd}` : 
                      `฿${product.priceBegin} - ฿${product.priceEnd}` }} / {{ product.nameUnit }}</td>
                          <td>{{ product.groupName }}</td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </v-card>
              </v-col>
            </v-col>
          </v-row>
        </v-row>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import L from "leaflet";
import { LMap, LTileLayer, LMarker, LTooltip, LPopup } from "vue2-leaflet";
import axios from "axios";

export default {
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LTooltip,
    LPopup,
  },
  data() {
    return {
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      zoom: 5.5,
      center: [17.87, 100.9925],
      bounds: null,
      showAmount: true,
      showDetails: false,
      isShowListProduct: false,
      selectedItem: 1,
      productByArea: [],
      productMk: [],
      nameTypeOfArgiMk: "",
      nameOfArgi: "",
      area: [],
      markers: [],
      pane: "Test",
      productByArgi: [],
      productByArgiList: [],
    };
  },
  methods: {
    productDetail(idProduct) {
      this.$router.push({
        name: "ProductDetail",
        params: { idProductOfMemberMk: idProduct },
      });
    },
    backFromArea() {
      this.zoomUpdated(5.5);
      this.showAmount = false;
      this.showDetails = true;
      this.isShowListProduct = false;
    },
    showProduct(idSellerMember, idAgri, lat, long) {
      console.log(lat, long);
      let position = {
        lat: lat,
        lng: long,
      };
      this.$refs.productMap.mapObject.flyTo(position, 12);
      // this.centerUpdated(position);
      let formData = new FormData();
      formData.append("idSellerMember", idSellerMember);
      formData.append("idAgri", idAgri);
      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/map/getProductFromArea.php",
          formData
        )
        .then((res) => {
          this.showDetails = false;
          this.isShowListProduct = true;
          this.productByArea = res.data.productByArea;
          console.log("productByArea", this.productByArea);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    itemClick(idArgi, nameOfArgi) {
      this.nameOfArgi = nameOfArgi;
      this.getProductByArgi(idArgi);
      this.showAmount = false;
      this.showDetails = true;
    },
    back() {
      this.showAmount = true;
      this.showDetails = false;
    },
    zoomUpdated(zoom) {
      this.zoom = zoom;
    },
    centerUpdated(center) {
      this.center = center;
    },
    boundsUpdated(bounds) {
      this.bounds = bounds;
    },
    getProductByArgi(idArgi) {
      this.zoomUpdated(5.5);
      let formData = new FormData();
      formData.append("idAgri", idArgi);
      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/map/getProductByArgi.php",
          formData
        )
        .then((res) => {
          if (res.data.productByArgi !== 0) {
            this.productByArgi = res.data.productByArgi;
            this.productByArgiList = this.productByArgi.filter(
              (thing, index, self) =>
                self.findIndex(
                  (t) =>
                    t.idSellerMember === thing.idSellerMember &&
                    t.idSellerMember === thing.idSellerMember
                ) === index
            );
            console.log("productByArgi", this.productByArgi);
            console.log("productByArgiList", this.productByArgiList);
            this.markers = this.productByArgiList.map((e, i) => {
              return {
                id: i,
                position: { lat: parseFloat(e.lat), lng: parseFloat(e.long) },
                tooltip: e.areaName,
                idArea: e.idArea,
                idAgri: e.idAgri,
                idSellerMember: e.idSellerMember,
                areaName: e.areaName,
                visible: true,
                lat: parseFloat(e.lat),
                lng: parseFloat(e.long),
              };
            });
            console.log("markers", this.markers);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    productByType() {
      let formData = new FormData();
      formData.append("idTypeOfArgi", this.$route.params.idTypeOfArgi);
      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/admin/api/map/getProductByType.php",
          formData
        )
        .then((res) => {
          if (res.data.productMk !== 0) {
            this.productMk = res.data.productMk;
            this.productMk = this.productMk.filter(
              (thing, index, self) =>
                self.findIndex(
                  (t) =>
                    t.nameArgi === thing.nameArgi &&
                    t.nameArgi === thing.nameArgi
                ) === index
            );
            console.log("productMk", this.productMk);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.nameTypeOfArgiMk = this.$route.params.nameTypeOfArgi;
    this.productByType();
  },
};
</script>



