<template>
  <v-col md="4">
    <v-img
      v-if="dataUrl"
      :src="dataUrl"
      :alt="file.name"
    >
    </v-img>
  </v-col>
</template>
<script>
export default {
  props: ["file"],
  data() {
    return {
      dataUrl: null,
    };
  },
  created() {
    const reader = new FileReader();

    reader.addEventListener(
      "load",
      () => {
        this.dataUrl = reader.result;
      },
      false
    );

    reader.readAsDataURL(this.file);
  },
};
</script>


